import React, {useCallback, useEffect, useState} from 'react'
import {TOKEN_NAME} from '../../Common/configset'
import DashboardWrapContainer from '../../Containers/DashboardWrapContainer'
import Header from '../../Components/Text/Header'
import WhiteBoxContainer from '../../Containers/WhiteBoxContainer'
import NormalInput from '../../Components/Input/NormalInput'
import RegisterButton from '../../Components/Button/RegisterButton'
import NormalFileInput from '../../Components/Input/NormalFileInput'
import {getToken} from '../../Common/tokenFunctions'
import SubNavigation from '../../Components/Navigation/SubNavigation'
import InnerBodyContainer from '../../Containers/InnerBodyContainer'
import {ROUTER_MENU_LIST} from '../../Common/routerset'
import {getParameter, getRequest, postRequest} from '../../Common/requestFunctions'
import {uploadTempFile} from '../../Common/fileFuctuons'
import ListHeader from '../../Components/Text/ListHeader'
import OldFileInput from '../../Components/Input/OldFileInput'
import RadioInput from '../../Components/Input/RadioInput'
import NormalNumberInput from '../../Components/Input/NormalNumberInput'
import DaumPostcode from 'react-daum-postcode'
import FullAddInput from '../../Components/Input/FullAddInput'
import CustomIndexInput from '../../Components/Input/CustomIndexInput'


//   
// ! isUpdate true     
const OutsourcingRegister = () => {

  const [pk, setPk] = useState<string>('')
  const [name, setName] = useState<string>('')
  const [no, setNo] = useState<number>()
  const [type, setType] = useState<number>(0) //0: , 1:
  const [phone, setPhone] = useState<string>('')
  const [address, setAddress] = useState<string>('')
  const [subAddress, setSubAddress] = useState<string>('')
  const [fax, setFax] = useState<string>('')
  const [phoneM, setPhoneM] = useState<string>('')
  const [emailM, setEmailM] = useState<string>('')
  const [email, setEmail] = useState<string>('')
  const [manager, setManager] = useState<string>('')
  const [ceo, setCeo] = useState<string>('')
  const [infoList, setInfoList] = useState<IInfo[]>([])
  const [isOpen, setIsOpen] = useState<boolean>(false)
  const [paths, setPaths] = useState<any[1]>([null])
  const [oldPaths, setOldPaths] = useState<any[1]>([null])

  const [isUpdate, setIsUpdate] = useState<boolean>(false)


  useEffect(() => {
    if (getParameter('pk') !== '') {
      setPk(getParameter('pk'))
      ////alert(`   - pk :` + param)
      setIsUpdate(true)
      getData()
    }

  }, [])


  /**
   * addFiles()
   *  
   * @param {object(file)} event.target.files[0] 
   * @returns X
   */
  const addFiles = async (event: any, index: number): Promise<void> => {
    if (event.target.files[0] === undefined) {

      return
    }
    if (event.target.files[0].type.includes('image')) { // 

      const tempFile = event.target.files[0]
      const res = await uploadTempFile(event.target.files[0])

      if (res !== false) {
        const tempPatchList = paths.slice()
        tempPatchList[index] = res
        setPaths(tempPatchList)
        return
      } else {
        return
      }

    } else {

      //alert('   .')
    }

  }


  /**
   * getData()
   *     
   * @param {string} url  
   * @param {string} pk  pk
   * @returns X
   */
  const getData = useCallback(async () => {

    const res = await getRequest('http://255.255.255.255:8299/api/v1/customer/view?pk=' + getParameter('pk'), getToken(TOKEN_NAME))

    if (res === false) {
      //TODO:  
    } else {
      if (res.status === 200) {
        const data = res.results
        setName(data.name)
        setPk(data.pk)
        setNo(Number(data.number))
        setType(Number(data.type))
        setPk(data.pk)
        setCeo(data.ceo)
        setOldPaths([data.photo])
        setPhone(data.telephone)
        setEmailM(data.manager_email)
        setPhoneM(data.manager_phone)
        setManager(data.manager)
        setEmail(data.ceo_email)

        //setInfoList(data.info_list)
        setAddress(data.address)
        setFax(data.fax)

      } else {
        //TODO:   
      }
    }
  }, [pk, name, no, type, ceo, paths, oldPaths, phone, emailM, email, phone, phoneM, address, fax])

  /**
   * onsubmitFormUpdate()
   *    
   * @param {string} url  
   * @param {string} pk  pk
   * @param {string} name 
   * @param {string} no 
   * @param {object(file)} file  
   * @param {string} info 
   * @param {string} made 
   * @param {string} type 
   * @param {string} madeNo 
   * @returns X
   */
  const onsubmitFormUpdate = useCallback(async (e) => {
    e.preventDefault()
    if (name === '') {
      //alert("  .  .")
      return
    }

    const data = {
      pk: getParameter('pk'),
      name: name,
      number: no,
      type: type,
      pathceo: ceo,
      photo: paths[0],
      telephone: phone,
      ceo_email: email,
      manager: manager,
      manager_phone: phoneM,
      manager_email: emailM,
      address: address,
      fax: fax,
      info_list: infoList.length > 0 ? JSON.stringify(infoList) : null,

    }

    const res = await postRequest('http://255.255.255.255:8299/api/v1/customer/update/', data, getToken(TOKEN_NAME))

    if (res === false) {
      ////alert('      .')
    } else {
      if (res.status === 200) {
        //alert('  ')
        setIsUpdate(false)
      } else {
        ////alert('      .')
      }
    }

  }, [pk, name, no, type, ceo, paths, oldPaths, phone, emailM, email, phone, phoneM, address, fax, manager])

  /**
   * onsubmitForm()
   *   
   * @param {string} url  
   * @param {string} name 
   * @param {string} no 
   * @param {string} info 
   * @param {string} made 
   * @param {string} type 
   * @param {string} madeNo 
   * @returns X
   */
  const onsubmitForm = useCallback(async (e) => {
    e.preventDefault()
    ////alert(JSON.stringify(infoList))
    if (name === '') {
      //alert("  .  .")
      return
    }
    const data = {

      name: name,
      number: no,
      type: type,
      pathceo: ceo,
      photo: paths[0],
      telephone: phone,
      ceo_email: email,
      manager: manager,
      manager_phone: phoneM,
      manager_email: emailM,
      address: address,
      fax: fax,
      info_list: infoList.length > 0 ? JSON.stringify(infoList) : null,

    }


    const res = await postRequest('http://255.255.255.255:8299/api/v1/outsourcing/register', data, getToken(TOKEN_NAME))

    if (res === false) {
      //TODO:  
    } else {
      if (res.status === 200) {
        //alert('  ')
        const data = res.results
        setName('')
        setPk('')
        setNo(undefined)
        setType(0)

        setCeo('')
        setPaths([null])
        setOldPaths([null])
        setPhone('')
        setEmailM('')
        setPhoneM('')
        setEmail('')

        setInfoList([])
        setAddress('')
        setFax('')

      } else {
        //TODO:   
      }
    }

  }, [pk, name, no, type, ceo, paths, oldPaths, phone, emailM, email, phone, phoneM, address, fax, manager])

  const handleComplete = useCallback((data) => {
    let fullAddress = data.address
    let extraAddress = ''

    if (data.addressType === 'R') {
      if (data.bname !== '') {
        extraAddress += data.bname
      }
      if (data.buildingName !== '') {
        extraAddress += (extraAddress !== '' ? `, ${data.buildingName}` : data.buildingName)
      }
      fullAddress += (extraAddress !== '' ? ` (${extraAddress})` : '')
    }
    setIsOpen(false)
    setAddress(fullAddress + ' ')

  }, [address, isOpen])


  return (
    <DashboardWrapContainer index={3}>
      <SubNavigation list={ROUTER_MENU_LIST[3]}/>
      <InnerBodyContainer>
        <Header title={isUpdate ? ' ' : ' '}/>
        <WhiteBoxContainer>
          <form onSubmit={isUpdate ? onsubmitFormUpdate : onsubmitForm}>
            <ListHeader title=" "/>
            <NormalInput title={' '} value={name} onChangeEvent={setName}
                         description={'  '}/>
            <NormalInput title={' '} value={ceo} onChangeEvent={setCeo}
                         description={'   '}/>
            <RadioInput title={' '} target={type} onChangeEvent={setType}
                        contents={[{value: 0, title: ''}, {value: 1, title: ''}]}/>
            <NormalNumberInput title={' '} value={no} onChangeEvent={setNo}
                               description={'   (-)'} returnType={'string'}/>


            <br/>
            <ListHeader title=" "/>
            <NormalFileInput title={'  '} name={paths[0]} thisId={'photo'}
                             onChangeEvent={(e) => addFiles(e, 0)}
                             description={isUpdate ? oldPaths[0] : '     '}/>
            {
              isUpdate ?
                <OldFileInput title={'  '} urlList={oldPaths} nameList={['']} isImage={true}/>
                :
                null
            }


            <div onClick={() => setIsOpen(true)}>
              <NormalInput title={' '} value={address} onChangeEvent={setAddress}
                           description={'    '}/>
            </div>
            {
              isOpen &&
              <DaumPostcode
                  onComplete={handleComplete}

              />
            }
            <NormalInput title={'  '} value={phone} onChangeEvent={setPhone}
                         description={'    '}/>
            <NormalInput title={' '} value={email} onChangeEvent={setEmail}
                         description={'  '}/>
            <NormalInput title={'  FAX'} value={fax} onChangeEvent={setFax}
                         description={'  '}/>
            <NormalInput title={' '} value={manager} onChangeEvent={setManager}
                         description={' ()  '}/>
            <NormalInput title={' '} value={phoneM} onChangeEvent={setPhoneM}
                         description={' ()  '}/>
            <NormalInput title={' '} value={emailM} onChangeEvent={setEmailM}
                         description={' ()  '}/>
            {/*  */}
            <FullAddInput title={' '} onChangeEvent={() => {
              const tempInfo = infoList.slice()
              tempInfo.push({title: `  ${infoList.length + 1}`, value: ''})
              setInfoList(tempInfo)
            }}>
              {
                infoList.map((v: IInfo, i) => {
                  return (
                    <CustomIndexInput index={i} value={v}
                                      onRemoveEvent={() => {
                                        const tempInfo = infoList.slice()
                                        tempInfo.splice(i, 1)
                                        setInfoList(tempInfo)
                                      }}
                                      onChangeEvent={(obj: IInfo) => {
                                        const tempInfo = infoList.slice()
                                        tempInfo.splice(i, 1, obj)
                                        setInfoList(tempInfo)
                                      }}
                    />
                  )
                })
              }
            </FullAddInput>


            <RegisterButton name={isUpdate ? '' : ''}/>
          </form>
        </WhiteBoxContainer>

      </InnerBodyContainer>

    </DashboardWrapContainer>

  )
}


export default OutsourcingRegister

