import React, {useCallback, useEffect, useState,} from 'react'
import Styled from 'styled-components'
import OvertonTable from '../../Components/Table/OvertonTable'
import LineTable from '../../Components/Table/LineTable'
import {API_URLS, getSegmentList, postSegmentDelete} from '../../Api/mes/process'
import {useHistory} from 'react-router-dom'
import {transferCodeToName} from '../../Common/codeTransferFunctions'
import NumberPagenation from '../../Components/Pagenation/NumberPagenation'
import Notiflix from "notiflix";

Notiflix.Loading.Init({svgColor: "#1cb9df",});

const SegmentListContainer = () => {
    const [page, setPage] = useState<PaginationInfo>({
        current: 1,
    })

    const [list, setList] = useState<any[]>([])
    const [titleEventList, setTitleEventList] = useState<any[]>([])
    const [detailList, setDetailList] = useState<any[]>([])
    const [deletePk, setDeletePk] = useState<({ pk: string[] })>({pk: []})
    const [index, setIndex] = useState({name: '  '})
    const [subIndex, setSubIndex] = useState({order: ' '})
    const [selectPk, setSelectPk] = useState<any>(null)
    const [selectMold, setSelectMold] = useState<any>(null)
    const [selectValue, setSelectValue] = useState<any>(null)
    const history = useHistory()

    const indexList = {
        segment: {
            name: '  ',
            status: ''
        }
    }


    const detailTitle = {
        segment: {
            order: ' ',
            type: ' ',
            detail_order: '  ',
            machine_name: '',
            mold_name: ' ',
        },
    }

    const dummy = [
        {
            process_name: ' 01',
            material_name: '()',
            status: ''
        },
        {
            process_name: '',
            material_name: '()',
            status: 'Off'
        },
        {
            process_name: '',
            material_name: '()',
            status: ''
        },
        {
            process_name: '',
            material_name: '()',
            status: ''
        },
        {
            process_name: '',
            material_name: '()',
            status: ''
        },
    ]

    const titleeventdummy = [
        {
            Name: '',
            Width: 90,
            Link: () => history.push('/process/detail/register')
        },
        {
            Name: '',
            Link: () => postDelete()
        }
    ]

    const onClick = useCallback((mold) => {
        if (mold.pk === selectPk) {
            setSelectPk(null)
            setSelectMold(null)
            setSelectValue(null)
        } else {
            setSelectPk(mold.pk)
            setSelectMold(mold.mold_name)
            setSelectValue(mold)
            //TODO: api 
            getData(mold.pk)
        }

    }, [list, selectPk])

    const arrayDelete = () => {
        while (true) {
            deletePk.pk.pop()
            if (deletePk.pk.length === 0) {
                break
            }
        }
    }

    const allCheckOnClick = useCallback((list) => {
        let tmpPk: string[] = []

        {
            list.length === 0 ?
                arrayDelete()
                :
                list.map((v, i) => {
                    arrayDelete()

                    if (deletePk.pk.indexOf(v.pk) === -1) {
                        tmpPk.push(v.pk)
                    }

                    tmpPk.map((vi, index) => {
                        if (deletePk.pk.indexOf(v.pk) === -1) {
                            deletePk.pk.push(vi)
                        }
                    })

                    if (tmpPk.length < deletePk.pk.length) {
                        deletePk.pk.shift()
                    }

                })
        }
    }, [deletePk])

    const checkOnClick = useCallback((Data) => {
        let IndexPk = deletePk.pk.indexOf(Data.pk)
        {
            deletePk.pk.indexOf(Data.pk) !== -1 ?
                deletePk.pk.splice(IndexPk, 1)
                :
                deletePk.pk.push(Data.pk)
        }
    }, [deletePk])

    const postDelete = useCallback(async () => {
        if (deletePk.pk.length <= 0) {
            alert('   .')
            return
        }
        const tempUrl = `${API_URLS['segment'].delete}`
        const res = await postSegmentDelete(tempUrl, deletePk)

        arrayDelete()
        getList()
    }, [deletePk])


    const getData = useCallback(async (pk) => {
        //TODO: 
        const tempUrl = `${API_URLS['segment'].load}?pk=${pk}`
        const res = await getSegmentList(tempUrl)
        // name: ""
        // pk: "2QQ90E_segment0"
        // processes: [{process_pk: "v1_SIZL_process_0_null_ ", process_name: " ", process_type: "0",}]
        // 0: {process_pk: "v1_SIZL_process_0_null_ ", process_name: " ", process_type: "0",}
        // machines: [{machine_type: 1, machine_names: " ", mold_name: "-", machine_name: " "}]
        // order: 0
        // process_name: " "
        // process_pk: "v1_SIZL_process_0_null_ "
        // process_type: "0"
        const getprocesses = res.processes.map((v, i) => {
            const processType = transferCodeToName('process', Number(v.process_type))
            const machine_name = v.machines.map((v, i) => {
                return v.machine_name
            })

            const mold_name = v.machines.map((v, i) => {
                return v.mold_name
            })
            return {
                ...v,
                order: (i + 1) + '',
                type: processType,
                detail_order: processType + ' ' + (i + 1) + '',
                machine_name: machine_name,
                mold_name: mold_name
            }
        })

        setDetailList(getprocesses)

    }, [detailList])

    const getDelete = useCallback(async () => {
        //TODO: 
        const tempUrl = `${API_URLS['segment'].delete}`
        const res = await getSegmentList(tempUrl)


        setDetailList(res)

    }, [detailList])

    const getList = useCallback(async () => { // useCallback
        //TODO: 
        Notiflix.Loading.Circle();
        const tempUrl = `${API_URLS['segment'].list + '?page='}${page.current}&limit=15`
        const res = await getSegmentList(tempUrl)

        setPage({current: res.current_page, total: res.total_page})

        setList(res.info_list)
        Notiflix.Loading.Remove()
    }, [list, page])

    useEffect(() => {
        // getList()
        setIndex(indexList['segment'])
        // setList(dummy)
        setTitleEventList(titleeventdummy)
        // setDetailList(detaildummy)
        setSubIndex(detailTitle['segment'])
    }, [])

    useEffect(() => {
        getList()
    }, [page.current])

    return (
        <div>
            <OvertonTable
                title={' ( )'}
                allCheckOnClickEvent={allCheckOnClick}
                titleOnClickEvent={titleEventList}
                indexList={index}
                valueList={list}
                clickValue={selectValue}
                checkOnClickEvent={checkOnClick}
                mainOnClickEvent={onClick}
                currentPage={page.current}
                totalPage={page.total}
                pageOnClickEvent={(event, i) => setPage({...page, current: i})}>
                {
                    selectPk !== null ?
                        <LineTable title={''} contentTitle={subIndex} contentList={detailList} objectLine={true}>
                            <Line/>
                        </LineTable>
                        :
                        null
                }
            </OvertonTable>
        </div>
    )
}

const Line = Styled.hr`
    margin: 10px 20px 12px 0px;
    border-color: #353b48;
    height: 1px;
    background-color: #353b48;
`

export default SegmentListContainer
