import React, {useCallback, useEffect, useState} from 'react'
import Styled from 'styled-components'
import DashboardWrapContainer from '../../Containers/DashboardWrapContainer'
import 'react-dropdown/style.css'
import InnerBodyContainer from '../../Containers/InnerBodyContainer'
import HeaderLive from '../../Components/Text/HeaderLive'
import MonitoringToggle from '../../Components/Toggle/MonitoringToggle'
import MonitoringTabs from '../../Components/Tabs/MonitoringTabs'
import MonitoringCard from '../../Components/Card/MonitoringCard'
import SearchModalContainer from '../../Containers/SearchModalContainer'
import SearchedList from '../../Components/List/SearchedList'
import {transferCodeToName} from '../../Common/codeTransferFunctions'
import {useHistory} from 'react-router-dom'
import MonitoringVerticalTable from '../../Components/Table/MonitoringVerticalTable'
import {API_URLS, getLoadTonList} from '../../Api/pm/monitoring'
import {API_URLS as MAP_URLS} from '../../Api/pm/map'
import MapBoard from '../../Components/Map/MapBoard'

//  
const PressMonitoring = () => {

  const [arrayType2, setArrayType2] = useState<number>(0) //[' ' , ' ']

  const [list, setList] = useState<IMonitoringList[]>([])
  const history = useHistory()
  const [title, setTitile] = useState<string>('')
  const [arrayType, setArrayType] = useState<number>(0) //['' , '']
  const [statusFilter, setStatusFilter] = useState<number>(-10)
  const [openList, setOpenList] = useState<string[]>([])
  const [selectedList, setSelectedList] = useState<number[]>([])
  const [checkList, setCheckList] = useState<number[]>([])
  const [isPopup, setIsPopup] = useState<boolean>(false)
  const [optionFilter, setOptionFilter] = useState<number | string>('all')
  const [optionList, setOptionList] = useState<number[]>([])
  const [isFirstLoad, setIsFirstLoad] = useState<boolean>(false)

  const onClickRefresh = useCallback(() => {
    setStatusFilter(1000)
    setCheckList([])
    setSelectedList([])

  }, [selectedList, statusFilter, checkList])
  /**
   * getData()
   *   
   * @param {string} url  
   * @returns X
   */
  const getList = useCallback(async () => {
    if (document.hidden) { // Opera 12.10 and Firefox 18 and later support
      //setCount(999)
      return
    }

    const tempUrl = `${API_URLS['press'].monitoring}`
    const resultData = await getLoadTonList(tempUrl)
    setIsFirstLoad(true)
    const data = resultData
    setList(data)
    if (data && data.info_list) {
      const arr = data[0].info_list!.map((v, i) => {
        return (v['title'])
      })
      setOptionList(arr)
    }
  }, [list, optionList])

  useEffect(() => {
    getList()
    //setList(dataSet.commonMonitoring)
    setIsFirstLoad(true)
    setTitile('')
  }, [])

  useEffect(() => {

    const interval = setInterval(() => {
      getList()
    }, 3000)

    return () => {
      clearTimeout(interval)
    }
  }, [])

  /**
   * onClickFilter()
   *   
   * @param {string} filter  
   * @returns X
   */
  const onClickFilter = useCallback((filter: number | string) => {
    setOptionFilter(filter)

  }, [optionFilter, list])

  return (
    <DashboardWrapContainer index={'monitoring'}>

      <InnerBodyContainer>
        <div style={{position: 'relative'}}>
          <HeaderLive title={title + ' '} isTurn={isFirstLoad}/>
          {/*{*/}
          {/*  arrayType2 === 1 &&*/}
          {/*  <div style={{position: 'absolute', display: 'inline-block', top: 0, right: 0}}>*/}
          {/*      <MonitoringToggle contents={[' ', ' ']} select={arrayType}*/}
          {/*                        onClickEvent={setArrayType}/>*/}

          {/*  </div>*/}
          {/*}*/}

        </div>
        <div style={{textAlign: 'left', marginBottom: 21}}>
          <MonitoringToggle contents={['  ', '  ']} select={arrayType2}
                            onClickEvent={setArrayType2}/>
        </div>
        {
          arrayType2 === 0 ?
            <MapBoard
              autoRendering={true}
              type={0}
              url={MAP_URLS['press'].monitoring}
            />
            :

            <>
              <WrapBox>
                <MonitoringTabs contents={
                  [{title: '', value: -10}, {title: '', value: -1}, {title: '', value: 10}, {
                    title: '',
                    value: 11
                  }, {title: '', value: -2}, {title: '', value: 0}]
                }
                                onClickEvent={setStatusFilter}
                />
                <div style={{position: 'absolute', display: 'inline-block', top: 0, right: 0}}>
                  {/*{*/}
                  {/*  openList !== undefined && openList !== null && openList === [] && openList.length === list.length ?*/}
                  {/*      <MonitoringOptionButton title={' '} color={'#b3b3b3'} onClickEvent={() => {*/}
                  {/*        setOpenList([])*/}
                  {/*      }}/>*/}
                  {/*      :*/}
                  {/*      <MonitoringOptionButton title={' '} color={'#b3b3b3'} onClickEvent={() => {*/}
                  {/*        if (list && list.length > 0)*/}
                  {/*          setOpenList(list.map((v: IMonitoringList) => {*/}
                  {/*                return v.pk!*/}
                  {/*              })*/}
                  {/*          )*/}
                  {/*      }}/>*/}
                  {/*}*/}
                  {/*<MonitoringOptionButton title={' '} onClickEvent={()=>history.push('/monitoring/full')} />*/}
                  {/*<MonitoringOptionButton title={' '} color={'#b3b3b3'} onClickEvent={()=>{setIsPopup(true);setCheckList(selectedList);}} />*/}
                  {/*<MonitoringOptionButton title={''} color={'#b3b3b3'} onClickEvent={()=>{onClickRefresh()}}/>*/}
                </div>
              </WrapBox><br/>

              {
                list && arrayType === 0 ?
                  list.length !== 0 ? (
                      list.map((v: IMonitoringList, i) => {
                        if (statusFilter === -10) {
                          return (
                            <>
                              <MonitoringCard key={'m-' + i} contents={v}
                                              optionList={selectedList.length === 0 ? v.info_list.map((m) => {
                                                return Number(m.title)
                                              }) : selectedList}
                                              onClickEvent={() => {
                                                if (openList.indexOf(v.pk) !== -1) { // 
                                                  setOpenList(openList.filter(f => f !== v.pk))
                                                } else {// 
                                                  setOpenList(openList.concat(v.pk))
                                                }
                                              }}
                                              isOpen={openList.indexOf(v.pk) == -1 ? false : true}/>
                            </>
                          )
                        } else {
                          if (v.operation === statusFilter) {
                            return (
                              <MonitoringCard key={'m-' + i} contents={v}
                                              optionList={selectedList.length === 0 ? v.info_list.map((m) => {
                                                return Number(m.title)
                                              }) : selectedList}
                                              onClickEvent={() => {
                                                if (openList.indexOf(v.pk) !== -1) { // 
                                                  setOpenList(openList.filter(f => f !== v.pk))
                                                } else {// 
                                                  setOpenList(openList.concat(v.pk))
                                                }
                                              }}
                                              isOpen={openList.indexOf(v.pk) == -1 ? false : true}/>
                            )
                          }
                        }
                      })
                    ) :
                    (<p style={{margin: 100}}> .</p>)
                  :

                  list && <MonitoringVerticalTable contents={list} operation={statusFilter}
                                                   filterList={selectedList.length === 0 && list.length > 0 ? list[0].info_list.map((m) => {
                                                     return Number(m.title)
                                                   }) : selectedList}/>
              }
            </>
        }
      </InnerBodyContainer>
      <SearchModalContainer
        onClickEvent={ //  
          () => {
            setSelectedList(checkList)

            setIsPopup(false)
          }}
        isVisible={isPopup} onClickClose={() => {
        setIsPopup(false)
      }} title={'  '}>

        <div style={{width: '100%', maxHeight: 380, overflowY: 'scroll'}}>
          {
            optionList.map((v, i) => {
              if (v == 903 || v == 902 || v == 901) {
                return
              }
              return (

                <SearchedList key={'s' + i} pk={String(v)} widths={['100%']}
                              contents={[transferCodeToName('title', v)]} isIconDimmed={false}
                              isSelected={checkList.find((c) => c === v) ? true : false}
                              onClickEvent={() => {
                                if (checkList.find((f) => f === v)) {
                                  setCheckList(checkList.filter((f) => f !== v))
                                } else {
                                  setCheckList(checkList.concat(v))
                                }

                              }}
                />

              )
            })
          }
        </div>
      </SearchModalContainer>
    </DashboardWrapContainer>

  )
}

const WrapBox = Styled.div`
    text-align: left;
    margin-top: 24px;
    position: relative;
    display: block;
    margin-bottom: 2px;
`

export default PressMonitoring
