import Axios from "axios";


/**
 * postRequest()
 * : axios post  +  
 * @param {string} url  
 * @param {object} data json  form 
 * @param {string} token 
 * @returns , (false)
 */
export const postRequest = async (url: string, data: object, token: string): Promise<any> => {

    const returnValue = await Axios.post(url,
         data,
         { 'headers': { 'Authorization': token } }
    )
    .then(function (res: IServerResponse) {
      return res.data
    })
    .catch(function (error) {
      console.log(error);
      return false
    });
    return returnValue
}

/**
 * getRequest()
 * : axios get  +  
 * @param {string} url  
 * @param {string} token 
 * @returns , (false)
 */
export const getRequest = async (url: string, token: string): Promise<any> => {

    const returnValue = await Axios.get(url, { 'headers': { 'Authorization': token } })
    .then(function (res: IServerResponse) {
      return res.data
    })
    .catch(function (error) {
      console.log(error);
      return false
    });

    return returnValue
}

