/**
 * getParameter()
 * ?key=     
 * @param {string} key 
 * @returns    value
 */

export const getParameter = (key) =>{
  key = key.replace(/[\[]/, "\\[").replace(/[\]]/, "\\]");
  var regex = new RegExp("[\\?&]" + key + "=([^&#]*)"),
      results = regex.exec(window.location.search);
  return results === null ? "" : decodeURIComponent(results[1].replace(/\+/g, " "));
}

