import client from '../Api/configs/admin';

/**
 * getCommonList()
 * /// 
 * @param {string} url  
 * @returns {Array} list
 * @author 
 */
export const getCommonList = async (url: string) => {
    const temp: IServerData = await client.get(url);
    if (temp.status === 400) {
        alert(' .')
        return
    }
    return temp.results!;
}

/**
 * saveCommonData()
 *   
 * @param {string} url  
 * @param {Object} data  
 * @returns {Boolean} true/false 
 * @author 
 */
export const saveCommonData = async (url: string, data: any) => {
    const temp: IServerData = await client.post(url, data);
    if (temp.status === 400) {
        alert(' .')
        return
    }
    if (temp) {
        return true
    } else {
        return false
    }
}

/**
 * getCommonData()
 *   
 * @param {string} url  
 * @returns {Object}  
 * @author 
 */
export const getCommonData = async (url: string) => {
    const temp: IServerData = await client.get(url);
    if (temp.status === 400) {
        alert(' .')
        return
    }
    return temp.results!;
}


