import axios from 'axios'
import {getToken} from '../../Common/tokenFunctions'
import {TOKEN_NAME} from '../../Common/configset'
import {SF_ENDPOINT} from '../SF_endpoint'

/**
 *
 * axios   
 * -   (  )
 * -   (  )
 */
const client = axios.create()

client.defaults.baseURL = SF_ENDPOINT + '/api'

client.defaults.headers.common['Authorization'] = getToken(TOKEN_NAME)

client.interceptors.response.use(function (response) {
    //console.log(response.data.status)
    const returnError = getErrorCase(response.data.status)

    if (returnError) {
        alert(returnError)
        return Promise.reject()
    } else {
        return response.data
    }

}, function (error) {
    console.error(error)
    //alert('SERVER ERROR]     .');
    return Promise.reject(error)

})

const getErrorCase = (code: any) => {
    switch (code) {
        case 2000:
            // alert('[ ]     ')
            return '[ ]     '
        case 3000:
            return '[ ]    .   .'
        case 1011:
            return '[ ]      '
        case 200:
            return false
        case '200':
            return false
        default:
            return '[RESPONSE ERROR]     .'
    }
}
export default client
