import React, {useCallback, useEffect, useState} from 'react';
import Styled from 'styled-components'
import {BASE_URL, BG_COLOR_SUB2, TOKEN_NAME} from '../../Common/configset'
import DashboardWrapContainer from '../../Containers/DashboardWrapContainer';
import Header from '../../Components/Text/Header';
import {getToken} from '../../Common/tokenFunctions';
import 'react-dropdown/style.css'
import {dataSet} from '../../Common/dataset'
import BasicDropdown from '../../Components/Dropdown/BasicDropdown';
import {getRequest} from '../../Common/requestFunctions';
import SubNavigation from '../../Components/Navigation/SubNavigation';
import {ROUTER_MENU_LIST} from '../../Common/routerset';
import InnerBodyContainer from '../../Containers/InnerBodyContainer';
import ItemList from '../../Components/List/ItemList';

//  
const LineList = () => {

    interface Line {
        pk: string,
        line_code: string,
        line_detail: string,
        item_list: IStatus[]
    }

    interface LineMachine {
        machine_code: string,
        machine_photo: string,
        machine_name: string,
        is_connected: boolean,
    }

    const [list, setList] = useState<Line[]>([]);
    const [option, setOption] = useState<number>(0);

    const optionList = [
        "", "()",
    ]
    /**
     * getList()
     *  
     * @param {string} url
     * @returns X
     */
    const getList = useCallback(async () => {

        const results = await getRequest(BASE_URL + '/api/v1/procedure/list/0', getToken(TOKEN_NAME))

        if (results === false) {
            ////alert('    .  .')
        } else {
            if (results.status === 200) {
                setList(results.results)
            } else {
                ////alert('    .  .')
            }
        }
    }, [list])

    /**
     * onClickFilter()
     *   
     * @param {string} filter  
     * @returns X
     */
    const onClickFilter = useCallback(async (filter: number) => {
        setOption(filter)
        ////alert(`  :  - filter : ${filter}` )

        const results = await getRequest(BASE_URL + '/api/v1/procedure/list/' + filter, getToken(TOKEN_NAME))

        if (results === false) {
            ////alert('    .  .')
        } else {
            if (results.status === 200) {
                setList(results.results)
            } else {
                ////alert('    .  .')
            }
        }
    }, [option])

    useEffect(() => {
        //getList()
        setList(dataSet.lineList)
    }, [])


    const onClickModify = useCallback((id) => {

        window.location.href = `/update/line?pk=${id}`

    }, [])

    return (
        <DashboardWrapContainer>
            <SubNavigation list={ROUTER_MENU_LIST[0]}/>
            <InnerBodyContainer>
                <div style={{position: 'relative'}}>
                    <Header title={'  '}/>
                    <div style={{position: 'absolute', display: 'inline-block', top: 0, right: 0, zIndex: 4}}>
                        <BasicDropdown select={optionList[option]} contents={optionList} onClickEvent={onClickFilter}/>
                    </div>
                </div>
                {
                    list.map((v: Line) => {
                        return (
                            <ItemList pk={v.pk} description={v.line_detail} title={v.line_code} contents={v.item_list}
                                      stock={v.item_list.length} onClickEvent={onClickModify}/>
                        )
                    })
                }

            </InnerBodyContainer>
        </DashboardWrapContainer>

    );
}
const FullPageDiv = Styled.div`
  width: 100%;
  height: 100%;
  color: white;
  background-color: ${BG_COLOR_SUB2}
`


export default LineList;
