import React, {useCallback, useEffect, useState,} from "react";
import Styled from "styled-components";
import OvertonTable from "../../Components/Table/OvertonTable";
import LineTable from "../../Components/Table/LineTable";
import {API_URLS, getStockList} from "../../Api/mes/manageStock";
import {useHistory} from "react-router-dom"
import NumberPagenation from "../../Components/Pagenation/NumberPagenation";
import {transferCodeToName} from "../../Common/codeTransferFunctions";
import Notiflix from "notiflix";

Notiflix.Loading.Init({svgColor: "#1cb9df",});

const StockListContainer = () => {

    const [list, setList] = useState<any[]>([]);
    const [titleEventList, setTitleEventList] = useState<any[]>([]);
    const [detailList, setDetailList] = useState<any>({
        machine_pk: "",
        machine_name: "",
        recommend: 0
    });
    const [index, setIndex] = useState({material_name: "()"});
    const [selectPk, setSelectPk] = useState<any>(null);
    const [selectStock, setSelectStock] = useState<any>(null);
    const [selectValue, setSelectValue] = useState<any>(null);
    const [subIndex, setSubIndex] = useState({writer: ""})
    const [page, setPage] = useState<PaginationInfo>({
        current: 1,
    });
    const history = useHistory()

    const indexList = {
        stock_list: {
            material_name: "()",
            material_type: " ",
            current_stock: "",
            location_name: "",
            safe_stock: "",
        }
    }

    const dummy = [
        {
            item_name: "00",
            stock_type: "",
            stock_quantity: "1,000,000",
            storage_location: "01",
            safety_stock: "9,999,999"
        },
        {
            item_name: "01",
            stock_type: "",
            stock_quantity: "1,000,000",
            storage_location: "01",
            safety_stock: "9,999,999"
        },
        {
            item_name: "02",
            stock_type: " ",
            stock_quantity: "1,000,000",
            storage_location: "01",
            safety_stock: "9,999,999"
        },
        {
            item_name: "03",
            stock_type: " ",
            stock_quantity: "1,000,000",
            storage_location: "01",
            safety_stock: "9,999,999"
        },
        {
            item_name: "04",
            stock_type: "",
            stock_quantity: "1,000,000",
            storage_location: "01",
            safety_stock: "9,999,999"
        }
    ]

    const detailTitle = {
        item_detailList: {
            writer: "",
            sortation: "",
            stock_quantity: "",
            before_quantity: " ",
            date: ""
        }
    }

    const detailValue = [
        {
            writer: "",
            sortation: " ",
            stock_quantity: "9,999,999,9999",
            before_quantity: "9,999,999,9999",
            date: "2020.08.09"
        },
        {
            writer: "",
            sortation: " ",
            stock_quantity: "9,999,999,9999",
            before_quantity: "9,999,999,9999",
            date: "2020.08.09"
        },
        {
            writer: "",
            sortation: " ",
            stock_quantity: "9,999,999,9999",
            before_quantity: "9,999,999,9999",
            date: "2020.08.09"
        },
        {
            writer: "",
            sortation: " ",
            stock_quantity: "9,999,999,9999",
            before_quantity: "9,999,999,9999",
            date: "2020.08.09"
        }
    ]


    const onClick = useCallback((stock) => {
        if (stock.pk === selectPk) {
            setSelectPk(null);
            setSelectStock(null);
            setSelectValue(null);
        } else {
            setSelectPk(stock.pk);
            setSelectStock(stock.item_name);
            setSelectValue(stock)
            //TODO: api 
            // getData(stock.pk)
        }

    }, [list, selectPk]);

    // const getData = useCallback(async (pk) => {
    //     //TODO: 
    //     const tempUrl = `${API_URLS['mold'].load}?pk=${pk}`
    //     const res = await getMoldData(tempUrl)
    //
    //     setDetailList(res)
    //
    // }, [detailList])

    const getList = useCallback(async () => { // useCallback
        //TODO: 
        Notiflix.Loading.Circle();
        const tempUrl = `${API_URLS['stock'].list}?type=-1&filter=-1&page=${page.current}&limit=15`
        const res = await getStockList(tempUrl)

        const getStock = res.info_list.map((v, i) => {
            const material_type = transferCodeToName('material', v.material_type)
            const current_stock = v.current_stock.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
            const safe_stock = v.safe_stock.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")

            return {...v, material_type: material_type, current_stock: current_stock, safe_stock: safe_stock}
        })

        setList(getStock)

        setPage({current: res.current_page, total: res.total_page})
        Notiflix.Loading.Remove()
    }, [list, page])

    useEffect(() => {
        getList()
        setIndex(indexList["stock_list"])
        // setList(dummy)
        setDetailList(detailValue)
        setSubIndex(detailTitle['item_detailList'])
    }, [])

    useEffect(() => {
        getList()
    }, [page.current])

    return (
        <div>
            <OvertonTable
                title={' '}
                titleOnClickEvent={titleEventList}
                indexList={index}
                valueList={list}
                currentPage={page.current}
                totalPage={page.total}
                pageOnClickEvent={(event, i) => setPage({...page, current: i})}
                noChildren={true}>
                {
                    selectPk !== null &&
                    <LineTable title={selectStock + '  '} contentTitle={subIndex} contentList={detailList}>
                        <Line/>
                    </LineTable>
                }
            </OvertonTable>
        </div>
    );
}

const Line = Styled.hr`
    margin: 10px 20px 12px 0px;
    border-color: #353b48;
    height: 1px;
    background-color: #353b48;
`

export default StockListContainer;
