import React, {useCallback, useEffect, useState} from 'react';
import Styled from 'styled-components'
import {BG_COLOR_SUB2, TOKEN_NAME} from '../../Common/configset'
import DashboardWrapContainer from '../../Containers/DashboardWrapContainer';
import Header from '../../Components/Text/Header';
import WhiteBoxContainer from '../../Containers/WhiteBoxContainer';
import NormalInput from '../../Components/Input/NormalInput';
import RegisterButton from '../../Components/Button/RegisterButton';
import {getToken} from '../../Common/tokenFunctions';
import InnerBodyContainer from '../../Containers/InnerBodyContainer';
import {getParameter, getRequest, postRequest} from '../../Common/requestFunctions';
import ListHeader from '../../Components/Text/ListHeader';
import SelectDocumentForm from '../../Containers/Basic/SelectDocumentForm';
import DocumentFormatInputList from '../../Containers/Basic/DocumentFormatInputList';
import {JsonStringifyList} from '../../Functions/JsonStringifyList';
import useObjectInput from '../../Functions/UseInput';
import RadioInput from '../../Components/Input/RadioInput';
import FileAddInput from '../../Components/Input/FileAddInput';
import NormalAddressInput from '../../Components/Input/NormalAddressInput';


//  
const Old_OutsourcingCompanyRegister = () => {

    const [document, setDocument] = useState<any>({id: '', value: '()'});

    const [inputData, setInputData] = useObjectInput('CHANGE', {
        name: '',
        pathceo: '',
        type: 0,
        photo: '',
        location: {
            postcode: '',
            roadAddress: '',
            detail: '',
        },
        telephone: null,
        ceo_email: '',
        fax: '',
        manager_phone: '',
        manager_email: '',
    });

    const [essential, setEssential] = useState<any[]>([]);
    const [optional, setOptional] = useState<any[]>([]);

    const [isUpdate, setIsUpdate] = useState<boolean>(false);
    const [pk, setPk] = useState<string>('');

    useEffect(() => {

        if (getParameter('pk') !== "") {
            setPk(getParameter('pk'))
            ////alert(`   - pk :` + param)
            setIsUpdate(true)
            getData()
        }

    }, [])

    const getData = useCallback(async () => {

        const res = await getRequest('http://255.255.255.255:8299/api/v1/outsourcing/load?pk=' + getParameter('pk'), getToken(TOKEN_NAME))

        if (res === false) {
            //TODO:  
        } else {
            if (res.status === 200 || res.status === "200") {
                inputData(res.results)

            } else {
                //TODO:   
            }
        }
    }, [pk, essential, optional, inputData])


    const onsubmitFormUpdate = useCallback(async (e) => {
        e.preventDefault();

        const data = {
            ...inputData,
            document_pk: document.pk,
            info_list: JsonStringifyList(essential, optional)
        }

        const res = await postRequest('http://255.255.255.255:8299/api/v1/outsourcing/update', data, getToken(TOKEN_NAME))

        if (res === false) {
            ////alert('////alert('[SERVER ERROR]     .')');
        } else {
            if (res.status === 200) {
                // //alert('  ')
            } else {
                ////alert('      .')
            }
        }

    }, [pk, essential, optional, inputData])

    const onsubmitForm = useCallback(async (e) => {
        e.preventDefault();

        const data = {
            ...inputData,
            document_pk: document.pk,
            info_list: JsonStringifyList(essential, optional)
        }

        const res = await postRequest('http://255.255.255.255:8299/api/v1/outsourcing/register', data, getToken(TOKEN_NAME))

        if (res === false) {
            //TODO:  
            ////alert('////alert('[SERVER ERROR]     .')');
        } else {
            if (res.status === 200) {
                //alert('  ')
            } else {
                //TODO:   
            }
        }

    }, [pk, essential, optional, inputData])


    return (
        <DashboardWrapContainer index={'basic'}>

            <InnerBodyContainer>
                <Header title={isUpdate ? ' ' : ' '}/>
                <WhiteBoxContainer>
                    {
                        document.id !== '' || isUpdate == true ?

                            <form onSubmit={isUpdate ? onsubmitFormUpdate : onsubmitForm}>
                                <ListHeader title=" "/>
                                <NormalInput title={''} value={inputData.name}
                                             onChangeEvent={(input) => setInputData(`name`, input)}/>
                                <NormalInput title={''} value={inputData.pathceo}
                                             onChangeEvent={(input) => setInputData(`pathceo`, input)}/>
                                <RadioInput title={' '} target={inputData.type}
                                            contents={[{value: 0, title: ''}, {value: 1, title: ''}]}
                                            onChangeEvent={(input) => setInputData(`type`, input)}/>
                                <NormalInput title={'/ '} value={inputData.number}
                                             onChangeEvent={(input) => setInputData(`number`, input)}/>
                                <FileAddInput title={'  '} target={`image`} description={`    .`}
                                              value={inputData.photo}
                                              onChangeEvent={(input) => setInputData(`photo`, input)}/>
                                <NormalInput title={' '} value={inputData.phone}
                                             onChangeEvent={(input) => setInputData(`telephone`, input)}/>

                                <NormalAddressInput title={' '} value={inputData.location}
                                                    onChangeEvent={(input) => setInputData(`location`, input)}
                                                    description={'  '}/>
                                <NormalInput title={' '} value={inputData.ceo_email}
                                             onChangeEvent={(input) => setInputData(`ceo_email`, input)}/>
                                <NormalInput title={' '} value={inputData.fax}
                                             onChangeEvent={(input) => setInputData(`fax`, input)}/>
                                <NormalInput title={''} value={inputData.manager}
                                             onChangeEvent={(input) => setInputData(`manager`, input)}/>
                                <NormalInput title={' '} value={inputData.manager_phone}
                                             onChangeEvent={(input) => setInputData(`manager_phone`, input)}/>
                                <NormalInput title={' '} value={inputData.manager_email}
                                             onChangeEvent={(input) => setInputData(`manager_email`, input)}/>

                                <DocumentFormatInputList pk={document.pk} onChangeEssential={setEssential}
                                                         onChangeOptional={setOptional}/>

                                <RegisterButton name={isUpdate ? '' : ''}/>

                            </form>
                            :
                            <SelectDocumentForm category={0} onChangeEvent={setDocument}/>

                    }
                </WhiteBoxContainer>

            </InnerBodyContainer>

        </DashboardWrapContainer>

    );
}
const FullPageDiv = Styled.div`
  width: 100%;
  height: 100%;
  color: white;
  background-color: ${BG_COLOR_SUB2}
`


export default Old_OutsourcingCompanyRegister;
