import React, {useCallback, useEffect, useState} from 'react';
import Styled from 'styled-components'
import {BG_COLOR_SUB2, TOKEN_NAME} from '../../Common/configset'
import DashboardWrapContainer from '../../Containers/DashboardWrapContainer';
import Header from '../../Components/Text/Header';
import {getToken} from '../../Common/tokenFunctions';
import 'react-dropdown/style.css'
import BasicDropdown from '../../Components/Dropdown/BasicDropdown';
import SubNavigation from '../../Components/Navigation/SubNavigation';
import {ROUTER_MENU_LIST} from '../../Common/routerset';
import InnerBodyContainer from '../../Containers/InnerBodyContainer';
import {getRequest, postRequest} from '../../Common/requestFunctions';
import SearchInputSmall from '../../Components/Input/SearchInputSmall';
import SmallButtonLink from '../../Components/Button/SmallButtonLink';
import InfoTable from '../../Components/Table/InfoTable';

import {useHistory} from 'react-router-dom'

const StockList = () => {
    const history = useHistory();

    const [list, setList] = useState<IMaterial[]>([]);
    const [option, setOption] = useState(0);
    const [keyword, setKeyword] = useState<string>('');
    const optionList = [
        "", "", ""
    ]
    const index = {
        material_name: ' ',
        material_code: ' ',
        distributor: '',
        stock: '',
    }


    /**
     * getSearchList()
     *  
     * @param {string} url
     * @returns X
     */
    const getSearchList = useCallback(async (e) => {
        e.preventDefault();
        const results = await getRequest('http://255.255.255.255:8299/api/v1/material/list?keyword=' + keyword + '&orderBy=' + option, getToken(TOKEN_NAME))
        if (results === false) {
            ////alert('    .  .')
        } else {
            if (results.status === 200) {
                setList(results.results)
                setKeyword('')
            } else {
                ////alert('    .  .')
            }
        }
    }, [list, option, keyword])


    /**
     * getList()
     *  
     * @param {string} url
     * @returns X
     */
    const getList = useCallback(async () => {

        const results = await getRequest('http://255.255.255.255:8299/api/v1/material/list?keyword=' + keyword + '&orderBy=' + option, getToken(TOKEN_NAME))
        if (results === false) {
            ////alert('    .  .')
        } else {
            if (results.status === 200) {
                setList(results.results)
            } else {
                ////alert('    .  .')
            }
        }
    }, [list, keyword, option])

    /**
     * onClickFilter()
     *   
     * @param {string} filter  
     * @returns X
     */
    const onClickFilter = useCallback(async (filter: number) => {
        setOption(filter)
        ////alert(`  :  - filter : ${filter}` )

        const results = await getRequest('http://255.255.255.255:8299/api/v1/material/list?keyword=' + keyword + '&orderBy=' + option, getToken(TOKEN_NAME))

        if (results === false) {
            ////alert('    .  .')
        } else {
            if (results.status === 200) {
                setList(results.results)
            } else {
                ////alert('    .  .')
            }
        }
    }, [option, list, keyword])

    useEffect(() => {
        getList()

    }, [])


    const onClickDelete = useCallback(async (id) => {

        const results = await postRequest('http://255.255.255.255:8299/api/v1/material/delete', {pk: id}, getToken(TOKEN_NAME))
        if (results === false) {
            //alert('   .   .')
        } else {
            if (results.status === 200) {
                getList()
            } else {
                //alert('   .   .')
            }
        }


    }, [])


    const onClickList = useCallback((id) => {
        history.push(`/stock/change/in?pk=${id}`)


    }, [])
    const onClickList2 = useCallback((id) => {
        history.push(`/stock/change/out?pk=${id}`)


    }, [])
    return (
        <DashboardWrapContainer index={8}>
            <SubNavigation list={ROUTER_MENU_LIST[8]}/>
            <InnerBodyContainer>
                <div style={{position: 'relative'}}>
                    <Header title={`  (${list.length})`}/>
                    <p style={{float: 'left', color: '#ffffff90'}}>  ,         .</p>
                    <div style={{position: 'absolute', display: 'inline-block', top: 0, right: 0, zIndex: 4}}>
                        <SmallButtonLink name="+  " link="/stock/change/in"/>
                        <SmallButtonLink name="+  " link="/stock/change/out"/>
                        <BasicDropdown select={optionList[option]} contents={optionList} onClickEvent={onClickFilter}/>
                    </div>
                </div>
                <SearchInputSmall
                    description={' '}
                    value={keyword}
                    onChangeEvent={(e) => {
                        setKeyword(e.target.value)
                    }}
                    onClickEvent={getSearchList}
                />

                <InfoTable indexList={index} pkKey={'pk'} type={'material'} onClickEvent={onClickList}
                           onClickEventName={''}
                           onClickEvent2={onClickList2} onClickEventName2={''} onClickLinkUrl="/stock/history?pk="
                           contents={list}/>

            </InnerBodyContainer>
        </DashboardWrapContainer>

    );
}
const FullPageDiv = Styled.div`
  width: 100%;
  height: 100%;
  color: white;
  background-color: ${BG_COLOR_SUB2}
`


export default StockList;
