import client from '../configs/basic';

/**
 * postOutsourcingDelete()
 *   
 * @param {string} url  
 * @param object
 * @returns {object} data object
 * @author 
 * @version 0.1
 */
export const postOutsourcingDelete = async (url: string, object: object) => {
    const temp: IServerData = await client.post(url, object);
    if (temp.status === 400) {
        alert(' .')
        return
    }
    return temp.status;
}

