import React, {useCallback, useEffect, useState} from 'react'
import Styled from 'styled-components'
import {BG_COLOR_SUB, TOKEN_NAME} from '../../Common/configset'
import DashboardWrapContainer from '../../Containers/DashboardWrapContainer'
import Header from '../../Components/Text/Header'
import {getToken, loadXHR, setToken} from '../../Common/tokenFunctions'
import 'react-dropdown/style.css'
import {getRequest, postRequest} from '../../Common/requestFunctions'
import InnerBodyContainer from '../../Containers/InnerBodyContainer'
import WhiteBoxContainer from '../../Containers/WhiteBoxContainer'
import BasicColorButton from '../../Components/Button/BasicColorButton'
import ProfileInput from '../../Components/Input/ProfileInput'
import ReadOnlyInput from '../../Components/Input/ReadOnlyInput'
import {useUser, useUserDispatch} from '../../Context/UserContext'
import {uploadTempFile} from '../../Common/fileFuctuons'
import {usePopupDispatch} from '../../Context/PopupContext'


const MyPage = () => {

    const [target, setTarget] = useState<IMmember>()
    const [pk, setPk] = useState<string>('')
    const [name, setName] = useState<string>('')
    const [rank, setRank] = useState<string>('')
    const [year, setYear] = useState<number>(0)
    const [joinDate, setJoinDate] = useState<string>('')
    const [joinType, setJoinType] = useState<string>('')
    const [status, setStatus] = useState<string>('')
    const [file, setFile] = useState<any>()
    const [photo, setPhoto] = useState<any | null>()
    const [path, setPath] = useState<string | null>(null)

    const User = useUser()
    const dispatch = useUserDispatch()
    const dispatchp = usePopupDispatch()
    /**
     * loadUserInfo()
     * :     user info dispatch
     * @returns X
     */
    const loadUserInfo = async () => {


        const results = await getRequest('http://255.255.255.255:8299/api/v1/user/load', getToken(TOKEN_NAME))

        if (results === false) {
            //TODO:  
        } else {
            if (results.status === 200) {
                dispatch({
                    type: 'SET_USER',
                    data: {
                        pk: User.pk,
                        email: User.email,
                        is_admin: User.is_admin,
                        appointment: User.appointment,
                        name: User.name,
                        profile_img: '',
                        is_login: true,
                    }
                })
                dispatch({
                    type: 'SET_USER',
                    data: {
                        pk: results.results.pk,
                        email: results.results.email,
                        is_admin: results.results.is_admin,
                        appointment: results.results.appointment,
                        name: results.results.name,
                        profile_img: results.results.profile_img,
                        is_login: true,
                    }
                })
                loadXHR(results.results.profile_img).then(function (blob) {
                    setToken('sizl_photo', blob)
                })

            } else {
                //TODO : 
                //alert('   :      ')
            }
        }
    }
    /**
     * onClickSave()
     *  
     * @param {string} pk  pk
     * @param {string} profile_img  
     * @returns X , (false)  
     */
    const onClickSave = useCallback(async () => {
        ////alert('[ ]    ...')
        //return;

        const data = {
            pk: User.pk,
            profile_img: path

        }
        const results = await postRequest('http://255.255.255.255:8299/api/v1/member/profile', data, getToken(TOKEN_NAME))

        if (results === false) {
            //alert('.    .')
            //TODO:  
        } else {
            if (results.status === 200) {
                //alert('  ')
                loadUserInfo()
            } else {
                //alert('.    .')
            }

        }

    }, [file, User, path])

    /**
     * getTarget()
     *   
     * @param {string} url  
     * @param {string} pk  pk
     * @returns X , (false)  
     */
    const getTarget = useCallback(async () => {

        const results = await getRequest('http://255.255.255.255:8299/api/v1/member/view?pk=' + encodeURIComponent(User.email), getToken(TOKEN_NAME))

        if (results === false) {
            //TODO:  
        } else {
            if (results.status === 200) {

                setTarget(results.results)
                setName(results.results.name)
                setJoinDate(results.results.join_date)
                setJoinType(results.results.join_type)
                setRank(results.results.appointment)
                setStatus(results.results.status)
                setYear(results.results.year)
                setPhoto(results.results.profile_img)
            } else {
                //alert(' .')
                window.location.href = '/manage/members'
            }
        }
    }, [target, joinType, joinDate, status, year, rank])


    useEffect(() => {
        dispatchp({
            type: 'CHANGE_MODE',
            data: {
                mode: 'mes'
            }
        })

        getTarget()

    }, [])


    /**
     * addFile()
     *   
     * @param {string} e.target.file 
     * @returns X
     */
    const addFile = useCallback(async (event: any): Promise<void> => {

        if (event.target.files[0] === undefined) {
            setFile(null)
            setPath(null)
            return
        }
        if (event.target.files[0].type.includes('image')) { // 


            setFile(event.target.files[0])
            const previewFile = URL.createObjectURL(event.target.files[0])
            setPreview(previewFile)
            const temp = await uploadTempFile(event.target.files[0])
            if (temp === false) {
                setPhoto(null)
                setFile(null)
                return
            } else {
                setPath(temp)

                setPreview(previewFile)
            }


        } else {

            setFile(null)
            //alert('   .')
        }

    }, [file, photo, path])

    const setPreview = useCallback((blobUrl) => {
        setPhoto(blobUrl)
    }, [photo])


    return (
        <DashboardWrapContainer>
            <InnerBodyContainer>
                <div style={{position: 'relative'}}>
                    <Header title={''}/>
                    <div style={{position: 'absolute', display: 'inline-block', top: 0, right: 0, zIndex: 4}}>

                    </div>
                </div>
                {

                }
                <WhiteBoxContainer>

                    <div>
                        <ReadOnlyInput title={''} value={name}/>
                        <ReadOnlyInput title={''} value={rank}/>
                        <ReadOnlyInput title={''} value={String(year)}/>
                        <ReadOnlyInput title={''} value={joinDate}/>
                        <ProfileInput photo={photo} title={' '} name={'profilePhoto'} thisId={'profilePhoto'}
                                      onChangeEvent={addFile}/>
                    </div>

                    <div style={{textAlign: 'center', marginTop: 31}}>
                        <BasicColorButton name=" " onClickEvent={onClickSave} width={'360px'}/>
                    </div>
                </WhiteBoxContainer>
            </InnerBodyContainer>
        </DashboardWrapContainer>

    )
}

const InputBox = Styled.input`
    width: 276px;
    height: 40px;
    border: solid 1px #b3b3b3;
    padding-left: 11px;
    font-size: 14px;
    font-weight: bold;
    background-color: transparent;
`

const ButtonBox = Styled.button`
    padding: 4px 24px 4px 24px;
    color: white;
    border-radius: 5px;
    background-color: ${BG_COLOR_SUB};
    border: 0;
    font-size: 14px;
    margin-left: 9px;
`
export default MyPage
