import React, {useCallback, useEffect, useState} from 'react'
import Styled from 'styled-components'
import {BG_COLOR_SUB3, TOKEN_NAME} from '../../Common/configset'
import DashboardWrapContainer from '../../Containers/DashboardWrapContainer'
import SubHeader from '../../Components/Text/SubHeader'
import moment from 'moment'
import 'moment/locale/ko'
import InnerBodyContainer from '../../Containers/InnerBodyContainer'
import IC_NEXT from '../../Assets/Images/ic_next_page.png'
import IC_BEFORE from '../../Assets/Images/ic_before_page.png'
import IC_REFRESH from '../../Assets/Images/ic_refresh.png'
import DotPagenation from '../../Components/Pagenation/DotPagenation'
import {getRequest, postRequest} from '../../Common/requestFunctions'
import {getToken} from '../../Common/tokenFunctions'
import TaskTable from '../../Components/Table/TaskTable'
import StatusCard2 from '../../Components/Card/StatusCard2'
import {changeDashboardStatusToColor, changeStatusToColor} from '../../Common/statusFunctions'

interface IWeekInfo {
    date: string,
    issue: number,
}

interface ITodayInfo {
    issue: number,
    waring: number,
    after: number
}

const dummy = []


//   
const Dashboard = () => {

    const [today, setToday] = useState<string>(moment().format('YYYY-MM-DD'))
    const [targetDate, setTargetDate] = useState<string>(moment().format('YYYY-MM-DD'))
    const [list, setList] = useState<IWeekInfo[]>([])
    const [filter, setFilter] = useState<number>(0)
    const [status, setStatus] = useState<IStatus2[]>(dummy)
    const [stock, setStock] = useState<number>(16)
    const [info, setInfo] = useState<ITodayInfo>({issue: 0, after: 0, waring: 0})
    const [dim, setDim] = useState<boolean>(true)
    const [page, setPage] = useState<number>(1)
    const [taskList, setTaskList] = useState<ITask[]>([])
    const [option, setOption] = useState<number>(0)
    const [taskFilter, setTaskFilter] = useState<number>(1000)


    const optionList = [
        '', ' ', ' ', ' ', ' ', '  ', '  '
    ]
    const indexList = {
        status: '',
        title: '',
        output_name: '',
        amount: '',
        process: '',
        worker: '',
        comments: ''
    }

    /**
     * getSunday()
     *   ( )  
     * @returns targetDate ('YYYY-MM-DD')
     */
    const getSunday = useCallback(() => {
        for (var i = 0; i < 7; ++i) {
            if (moment().add(0 - i, 'days').locale('en').format('dddd') === 'Sunday') {
                const targetDate = moment().add(0 - i, 'days').format('YYYY-MM-DD')
                return targetDate
            }
        }
    }, [])


    /**
     * onClickChangeDay()
     *       
     * @returns X
     */
    const onClickChangeDay = useCallback((day) => {
        setTargetDate(day)
    }, [targetDate])

    useEffect(() => {

        setList(
            [
                {date: '2020-05-31', issue: 0},
                {date: '2020-06-01', issue: 1},
                {date: '2020-06-02', issue: 0},
                {date: '2020-06-03', issue: 3},
                {date: '2020-06-04', issue: 4},
                {date: '2020-06-05', issue: 0},
                {date: '2020-06-06', issue: 0},
            ]
        )

        setInfo({
            issue: 3,
            after: 2,
            waring: 0
        })

        //setStatus(dataSet.statusList)
        getStatus(1)
        onClickFilter(0)
        //setTaskList(dataSet.taskList)
    }, [])

    /**
     * getStatus()
     *   
     * @param {string} url  
     * @param {string} pageIndex  
     * @returns X , (false)  
     */
    const getStatus = useCallback(async (index: number) => {
        const results = await getRequest('http://255.255.255.255:8299/api/v1/dashboard/machine/' + index, getToken(TOKEN_NAME))

        if (results === false) {
            ////alert('8087 :     .')
        } else {
            if (results.status === 200) {
                setStock(results.results.stock)
                setStatus(results.results.item_list)
            } else {
                //alert('8087 :     .')
            }
        }
    }, [page])

    /**
     * getTasks()
     *   
     * @param {string} url  
     * @returns X , (false)  
     */
    const getTasks = useCallback(async (index: number) => {
        const results = await getRequest('http://255.255.255.255:8299/api/v1/task/list/' + filter, getToken(TOKEN_NAME))

        if (results === false) {
            ////alert('8087 :     .')
        } else {
            if (results.status === 200) {
                setStock(results.results.stock)
                setStatus(results.results.item_list)
            } else {
                //alert('8087 :     .')
            }
        }
    }, [page])

    /**
     * onClickChangePage()
     *     
     * @param {string} index  
     * @returns X
     */
    const onClickChangePage = useCallback((index: number) => {
        ////alert(page + ' -> ' + index)
        if (stock < 7) {
            ////alert('')
            return
        }
        if (index < 1 || index > Math.ceil(stock / 6)) {
            ////alert('')
            return
        }
        setPage(index)
        getStatus(index)
        //getStatus(index)
    }, [page, stock])

    /**
     * onClickTaskStatus()
     *    
     * @param {string} pk  pk
     * @param {string} value 
     * @returns X
     */
    const onClickTaskStatus = useCallback(async (pk: string, value: string) => {
        ////alert(`  :  pk: ${pk} - status : ${value}` )
        //return;
        const data = {
            pk: pk,
            status: value
        }
        const results = await postRequest('http://255.255.255.255:8299/api/v1/task/status', data, getToken(TOKEN_NAME))

        if (results === false) {
            ////alert('       .')
        } else {
            if (results.status === 200) {
                //alert(' .')
                onClickFilter(0)
            } else {
                //alert('       .')
            }
        }
    }, [])

    /**
     * onClickFilter()
     *   
     * @param {string} filter  
     * @returns X
     */
    const onClickFilter = useCallback(async (filter: number) => {
        setOption(filter)
        ////alert(`  :  - filter : ${filter}` )
        //return;
        const results = await getRequest('http://255.255.255.255:8299/api/v1/task/list/' + filter, getToken(TOKEN_NAME))

        if (results === false) {
            ////alert('    .')
        } else {
            if (results.status === 200) {
                setTaskList(results.results)

            } else {
                //alert('    .')
            }
        }
    }, [option])
    return (
        <DashboardWrapContainer>
            <InnerBodyContainer>


                {/*   */}
                <div style={{width: '100%', marginTop: 29, textAlign: 'left'}}>
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <SubHeader title={' '}/>
                        <img src={IC_REFRESH} onClick={() => {
                            getStatus(1)
                            setPage(1)
                        }} style={{width: 20, height: 20, marginLeft: 7, cursor: 'pointer'}}/>

                    </div>

                    <div style={{
                        color: 'black',
                        borderRadius: 5,
                        height: '150',
                        backgroundColor: BG_COLOR_SUB3,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        marginTop: 13,
                        marginBottom: 30
                    }}>
                        <div style={{width: '4%'}}>
                            <img onClick={page > 1 ? () => onClickChangePage(page - 1) : () => {
                            }} src={IC_BEFORE}
                                 style={{cursor: 'pointer', width: 24, height: 24, margin: 10, zIndex: 3}}/>
                        </div>
                        <div style={{
                            flexWrap: 'wrap',
                            flexDirection: 'row',
                            marginTop: 25,
                            width: '90%',
                            marginBottom: 17,
                            textAlign: 'center'
                        }}>
                            <div className="p-limit"
                                 style={{display: 'flex', flexDirection: 'row', width: '100%', textAlign: 'center'}}>
                                {dummy !== undefined && dummy.length === 0 ?
                                    <p style={{color: '#ffffff', textAlign: 'center', marginLeft: '43%'}}>coming
                                        soon</p> :
                                    dummy.map((v: IStatus2, index) => {
                                        if ((page - 1) * 6 <= index) {
                                            if ((page - 1) * 6 + 5 >= index) {
                                                return (
                                                    <StatusCard2 target={v} key={index}/>
                                                )
                                            }
                                        }

                                    })
                                }
                            </div>

                            <div style={{marginTop: 7,}}>
                                <DotPagenation stock={Math.ceil(dummy.length / 6)} selected={page}
                                               onClickEvent={onClickChangePage}/>
                            </div>
                        </div>

                        <div style={{width: '4%'}}>
                            <img
                                onClick={page < Math.ceil(dummy.length / 6) ? () => onClickChangePage(page + 1) : () => {
                                }} src={IC_NEXT} style={{cursor: 'pointer', width: 24, margin: 10}}/>
                        </div>
                    </div>
                </div>
                {/*   */}
                <div style={{width: '100%', marginTop: 44, textAlign: 'left', marginBottom: 10}}>
                    <SubHeader title={'  '}/>
                    <span> </span>
                    {/*<FilterButton onClick={()=>setTaskFilter('ready')} style={{backgroundColor: changeStatusToColor('ready')}}></FilterButton>*/}

                    <div style={{display: 'inline-block', float: 'right',}}>
                        {/*<div style={{display:'flex', alignItems:'center'}}>*/}
                        {/*<ColorButtonLink url="/task/register" >{<><img src={IC_ADD}/>   </>}</ColorButtonLink>*/}
                        {/*<BasicDropdown contents={['', '']} select={['', ''][option]} onClickEvent={onClickFilter}/>*/}
                        {/*</div>*/}
                    </div>
                    <div style={{marginTop: 12}}>
                        <FilterButton onClick={() => setTaskFilter(1000)}
                                      style={{backgroundColor: changeDashboardStatusToColor(1000)}}></FilterButton>
                        <FilterButton onClick={() => setTaskFilter(11)}
                                      style={{backgroundColor: changeDashboardStatusToColor(11)}}></FilterButton>
                        <FilterButton onClick={() => setTaskFilter(10)}
                                      style={{backgroundColor: changeDashboardStatusToColor(10)}}></FilterButton>
                        <FilterButton onClick={() => setTaskFilter(16)}
                                      style={{backgroundColor: changeDashboardStatusToColor(16)}}></FilterButton>
                        <TaskTable indexList={indexList} keyName={'pk'} buttonName=''
                                   contents={taskFilter === 1000 ? taskList : taskList.filter(f => f.status === taskFilter)}
                                   onClickEvent={onClickTaskStatus}/>

                    </div>
                    {
                        taskList.length === 0 &&
                        <NullBox>
                            {/*  .*/} coming soon
                        </NullBox>
                    }

                </div>
            </InnerBodyContainer>
        </DashboardWrapContainer>

    )
}

const NullBox = Styled.div`
  background-color: ${BG_COLOR_SUB3};
  padding: 12px;
  color: #666d79;
  font-size: 17px;
  font-weight: bold; 
  text-align: center;
  border-radius: 5px;
  margin-top: 5px;
`

const FilterButton = Styled.div`
  padding: 4px 20px;
  font-size: 15px;
  font-weight: bold;
  color: black;
  display: inline-block;
  border-radius: 6px;
  margin-right: 10px;
  margin-bottom: 12px;
`
export default Dashboard
