import client from '../configs/basic';

/**
 * getItemSearch()
 *  
 * @param {string} url  
 * @param {string} item_name 
 * @returns {Array}  
 * @author 
 */
export const getItemSearch = async (url: string) => {
    const temp: IServerData = await client.get(url);
    if (temp.status === 400) {
        alert(' .')
        return
    }
    return temp
}

/**
 * getStockList()
 *   
 * @param {string} url  
 * @returns {Array}  
 * @author 
 */
export const getStockList = async (url: string) => {
    const temp: IServerData = await client.get(url);
    if (temp.status === 400) {
        alert(' .')
        return
    }
    return temp.results
}

