import React, {useCallback, useEffect, useState} from 'react';
import Styled from 'styled-components'
import {BASE_URL, BG_COLOR_SUB2, TOKEN_NAME} from '../../Common/configset'
import DashboardWrapContainer from '../../Containers/DashboardWrapContainer';
import Header from '../../Components/Text/Header';
import {getToken} from '../../Common/tokenFunctions';
import NormalTable from '../../Components/Table/NormalTable';
import 'react-dropdown/style.css'
import BasicDropdown from '../../Components/Dropdown/BasicDropdown';
import SubNavigation from '../../Components/Navigation/SubNavigation';
import {ROUTER_MENU_LIST} from '../../Common/routerset';
import InnerBodyContainer from '../../Containers/InnerBodyContainer';
import {getRequest, postRequest} from '../../Common/requestFunctions';


const MaterialList = () => {

    const [list, setList] = useState<IMaterial[]>([]);
    const [option, setOption] = useState(0);

    const optionList = [
        "", "", ""
    ]
    const index = {
        material_name: ' ',
        material_code: ' ',
        distributor: '',
        material_spec: '',
        stock: ''
    }

    /**
     * onClickFilter()
     *   
     * @param {string} filter  
     * @returns X
     */
    const onClickFilter = useCallback(async (filter: number) => {
        setOption(filter)
        const results = await getRequest('http://255.255.255.255:8299/api/v1/material/list/' + filter, getToken(TOKEN_NAME))

        if (results === false) {
            ////alert('    .  .')
        } else {
            if (results.status === 200) {
                setList(results.results)
            } else {
                ////alert('    .  .')
            }
        }
    }, [option])

    /**
     * getList()
     *  
     * @param {string} url
     * @returns X
     */
    const getList = useCallback(async () => {

        const results = await getRequest('http://255.255.255.255:8299/api/v1/material/list/0', getToken(TOKEN_NAME))

        if (results === false) {
            ////alert('    .  .')
        } else {
            if (results.status === 200) {
                setList(results.results)
            } else {
                ////alert('    .  .')
            }
        }
    }, [list])


    useEffect(() => {
        getList();

    }, [])

    const onClickModify = useCallback(async (id, stock) => {
        const results = await postRequest(BASE_URL + '/api/v1/material/stock/', {
            pk: id,
            stock: stock
        }, getToken(TOKEN_NAME))

        if (results === false) {
            //alert(' .  .')
        } else {
            if (results.status === 200) {
                //alert(' .')
                getList()

            } else {
                //alert(' .  .')
            }
        }

    }, [])

    return (
        <DashboardWrapContainer index={8}>
            <SubNavigation list={ROUTER_MENU_LIST[8]}/>
            <InnerBodyContainer>
                <div style={{position: 'relative'}}>
                    <Header title={'  '}/>
                    <div style={{position: 'absolute', display: 'inline-block', top: 0, right: 0, zIndex: 4}}>
                        <BasicDropdown select={optionList[option]} contents={optionList} onClickEvent={onClickFilter}/>
                    </div>
                </div>

                <NormalTable widthList={['253px', '130px', '130px', '270px', '180px']}
                             onChangeEvent={
                                 setList
                             }
                             indexList={index} keyName={'pk'} eventType="input" buttonName=''
                             onClickEvent={onClickModify}
                             contents={list}/>
            </InnerBodyContainer>
        </DashboardWrapContainer>

    );
}
const FullPageDiv = Styled.div`
  width: 100%;
  height: 100%;
  color: white;
  background-color: ${BG_COLOR_SUB2}
`


export default MaterialList;
