import client from '../configs/map';

/**
 * getMonitoringMapData();
 *   
 * @param {string} url  
 * @returns {Object} 
 * @author 
 */
export const getMonitoringMapData = async (url: string) => {
    const temp: IServerData = await client.get(url);
    if (temp.status === 400) {
        alert(' .')
        return
    }
    return temp.results!;
}

