import React, {useCallback, useEffect, useState} from 'react'
import Styled from 'styled-components'
import {BG_COLOR_SUB2, TOKEN_NAME} from '../../Common/configset'
import DashboardWrapContainer from '../../Containers/DashboardWrapContainer'
import Header from '../../Components/Text/Header'
import WhiteBoxContainer from '../../Containers/WhiteBoxContainer'
import NormalInput from '../../Components/Input/NormalInput'
import RegisterButton from '../../Components/Button/RegisterButton'
import {getToken} from '../../Common/tokenFunctions'
import SubNavigation from '../../Components/Navigation/SubNavigation'
import {ROUTER_MENU_LIST} from '../../Common/routerset'
import InnerBodyContainer from '../../Containers/InnerBodyContainer'
import {getParameter, getRequest, postRequest} from '../../Common/requestFunctions'
import DropdownInput from '../../Components/Input/DropdownInput'
import CustomIndexInput from '../../Components/Input/CustomIndexInput'
import FullAddInput from '../../Components/Input/FullAddInput'
import {getMaterialTypeList} from '../../Common/codeTransferFunctions'
import ListHeader from '../../Components/Text/ListHeader'

interface IInfo {
    title: string,
    value: string,
}

//  
// ! isUpdate true     
const RegisterMaterial = () => {

    const [pk, setPk] = useState<string>('')
    const [isUpdate, setIsUpdate] = useState<boolean>(false)
    const [made, setMade] = useState<string>('')
    const [code, setCode] = useState<string>('')
    const [spec, setSpec] = useState<string>('')
    const [name, setName] = useState<string>('')
    const [info, setInfo] = useState<IInfo[]>([])
    const [amount, setAmount] = useState<number>(0)
    const [type, setType] = useState<number>(0)
    const indexList = getMaterialTypeList('kor')

    //
    const [isPoupup, setIsPoupup] = useState<boolean>(false)
    const [isSearched, setIsSearched] = useState<boolean>(false)
    const [keyword, setKeyword] = useState<string>('')
    const [checkList, setCheckList] = useState<IMold[]>([])
    const [list, setList] = useState<IMold[]>([])
    const [searchList, setSearchList] = useState<IMold[]>([])


    useEffect(() => {
        //setIsSearched(true)
        //setSearchList(dataSet.moldList);
        if (getParameter('pk') !== '') {
            setPk(getParameter('pk'))
            ////alert(`   - pk :` + param)
            setIsUpdate(true)
            getData()
        }

    }, [])

    /**
     * getData()
     *     
     * @param {string} url  
     * @param {string} pk  pk
     * @returns X
     */
    const getData = useCallback(async () => {

        const res = await getRequest('http://255.255.255.255:8299/api/v1/material/view?pk=' + getParameter('pk'), getToken(TOKEN_NAME))

        if (res === false) {
            //TODO:  
        } else {
            if (res.status === 200) {
                const data = res.results

                setType(data.material_type)
                setName(data.material_name)
                setMade(data.distributor)
                setCode(data.material_code)
                setSpec(data.material_spec)
                setPk(data.pk)
                setInfo(data.info_list)

            } else if (res.status === 1001 || res.data.status === 1002) {
                //TODO:    
            } else {
                //TODO:   
            }
        }
    }, [pk, made, code, info, spec, name, list])

    /**
     * onsubmitForm()
     *   
     * @param {string} url  
     * @param {string} name 
     * @param {array} info  
     * @param {string} made 
     * @param {string} spec 
     * @param {string} code 
     * @returns X
     */
    const onsubmitForm = useCallback(async (e) => {
        e.preventDefault()
        //TODO: 
        ////alert(info)
        if (name == '') {
            //alert('   . ')
            return
        }


        let moldPk = new Array()
        if (list.length > 0) {
            moldPk.push(list[0].pk)
        }
        let infoString
        if (info.length > 0) {
            infoString = JSON.stringify(info)
        }
        ////alert(' :  - ' + amount + code + name + info + made + spec + info );
        //return;
        const data = {
            material_name: name,
            material_code: code,
            material_spec: spec,
            material_type: type,
            stock: amount,

            distributor: made,
            info_list: infoString
        }

        const res = await postRequest('http://255.255.255.255:8299/api/v1/material/register', data, getToken(TOKEN_NAME))

        if (res === false) {
            //TODO:  
        } else {
            if (res.status === 200) {
                //alert('  ')
                setName('')
                setCode('')
                setSpec('')

                setType(0)
                setMade('')
                setInfo([])

            } else {
                //alert(' .   .')
                //TODO:   
            }
        }

    }, [made, code, amount, name, spec, info, pk])


    /**
     * onsubmitFormUpdate()
     *   
     * @param {string} url  
     * @param {string} pk pk
     * @param {string} name 
     * @param {array} info  
     * @param {string} made 
     * @param {string} spec 
     * @param {string} code 
     * @returns X
     */
    const onsubmitFormUpdate = useCallback(async (e) => {
        e.preventDefault()
        //TODO: 
        ////alert(' :  - ' + pk +  code + name + info + made + spec + info );
        //return;


        let infoString
        if (info.length > 0) {
            infoString = JSON.stringify(info)
        }
        let moldPk = new Array()
        if (list.length > 0) {
            moldPk.push(list[0].pk)
        }
        const data = {
            pk: pk,
            material_name: name,
            material_code: code,
            material_spec: spec,

            material_type: type,
            distributor: made,
            info_list: infoString,
        }

        const res = await postRequest('http://255.255.255.255:8299/api/v1/material/update', data, getToken(TOKEN_NAME))

        if (res === false) {
            //TODO:  
        } else {
            if (res.status === 200) {
                //alert('  ')
            } else {
                //TODO:   
            }
        }

    }, [made, code, name, spec, info, pk, amount, list])


    return (
        <DashboardWrapContainer index={0}>
            <SubNavigation list={ROUTER_MENU_LIST[0]}/>
            <InnerBodyContainer>
                <Header title={isUpdate ? ' ' : '   ( /  / )'}/>
                <WhiteBoxContainer>
                    <form onSubmit={isUpdate ? onsubmitFormUpdate : onsubmitForm}>
                        <ListHeader title=" "/>
                        <NormalInput title={' '} value={name} onChangeEvent={setName} description={' '}/>
                        <DropdownInput title={' '} target={indexList[type]} contents={indexList}
                                       onChangeEvent={(v) => setType(v)}/>
                        <br/>
                        <ListHeader title=" "/>
                        <NormalInput title={' '} value={code} onChangeEvent={setCode}
                                     description={'    '}/>
                        <NormalInput title={''} value={spec} onChangeEvent={setSpec}
                                     description={'  () '}/>
                        <NormalInput title={''} value={made} onChangeEvent={setMade} description={' '}/>


                        <FullAddInput title={' '} onChangeEvent={() => {
                            const tempInfo = info.slice()
                            tempInfo.push({title: `  ${info.length + 1}`, value: ''})
                            setInfo(tempInfo)
                        }}>
                            {
                                info.map((v: IInfo, i) => {
                                    return (
                                        <CustomIndexInput index={i} value={v}
                                                          onRemoveEvent={() => {
                                                              const tempInfo = info.slice()
                                                              tempInfo.splice(i, 1)
                                                              setInfo(tempInfo)
                                                          }}
                                                          onChangeEvent={(obj: IInfo) => {
                                                              const tempInfo = info.slice()
                                                              tempInfo.splice(i, 1, obj)
                                                              setInfo(tempInfo)
                                                          }}
                                        />
                                    )
                                })
                            }
                        </FullAddInput>

                        <RegisterButton name={isUpdate ? '' : ''}/>
                    </form>
                </WhiteBoxContainer>

            </InnerBodyContainer>
        </DashboardWrapContainer>

    )
}
const FullPageDiv = Styled.div`
  width: 100%;
  height: 100%;
  color: white;
  background-color: ${BG_COLOR_SUB2}
`


export default RegisterMaterial
