import React, {useCallback, useEffect, useState,} from 'react'
import Styled from 'styled-components'
import OvertonTable from '../../Components/Table/OvertonTable'
import LineTable from '../../Components/Table/LineTable'
import {API_URLS, getOutsourcingList, postOutsourcingDelete, postOutsourcingList} from '../../Api/mes/outsourcing'
import {useHistory} from 'react-router-dom'
import {getCustomerData} from '../../Api/mes/customer'
import NumberPagenation from '../../Components/Pagenation/NumberPagenation'
import Notiflix from "notiflix";
import {postMoldState} from "../../Api/mes/manageMold";

Notiflix.Loading.Init({svgColor: "#1cb9df",});

const OrderContainer = () => {

    const [list, setList] = useState<any[]>([])
    const [detailEventList, setDetaileventList] = useState<any[]>([])
    const [titleEventList, setTitleEventList] = useState<any[]>([])
    const [eventList, setEventList] = useState<any[]>([])
    const [detailList, setDetailList] = useState<any[]>([])
    const [contentsList, setContentsList] = useState<any[]>(['', ''])
    const [option, setOption] = useState<number>(0)
    const [searchValue, setSearchValue] = useState<any>('')
    const [index, setIndex] = useState({company_name: ' '})
    const [subIndex, setSubIndex] = useState({manager: ''})
    const [deletePk, setDeletePk] = useState<({ pk: string[] })>({pk: []})
    const [selectPk, setSelectPk] = useState<any>(null)
    const [selectMaterial, setSelectMaterial] = useState<any>(null)
    const [selectValue, setSelectValue] = useState<any>(null)
    const [page, setPage] = useState<PaginationInfo>({
        current: 1,
    })
    const history = useHistory()

    const indexList = {
        order: {
            company_name: ' ',
            product: ' ',
            quantity: '',
            unpaid: ' ',
            ceo_name: ' ',
            registered: ' ',
        }
    }


    const detailTitle = {
        order: {
            manager: '',
            due_date: '',
            address: ' ',
            payment_condition: '  ',
            status: '',
        },
    }


    const detaildummy = [
        {
            writer: '',
            delivery_date: '2020.08.09',
            address: '   123-456 A 01',
            payment_condition: '  100  ',
            statement: '',
        },
    ]

    const arrayDelete = () => {
        while (true) {
            deletePk.pk.pop()
            if (deletePk.pk.length === 0) {
                break
            }
        }
    }

    const allCheckOnClick = useCallback((list) => {
        let tmpPk: string[] = []

        {
            list.length === 0 ?
                arrayDelete()
                :
                list.map((v, i) => {

                    if (deletePk.pk.indexOf(v.pk) === -1) {
                        tmpPk.push(v.pk)
                    }

                    tmpPk.map((vi, index) => {
                        if (deletePk.pk.indexOf(v.pk) === -1) {
                            deletePk.pk.push(vi)
                        }
                    })

                    if (tmpPk.length < deletePk.pk.length) {
                        deletePk.pk.shift()
                    }

                })
        }
    }, [deletePk])

    const checkOnClick = useCallback((Data) => {
        let IndexPk = deletePk.pk.indexOf(Data.pk)
        {
            deletePk.pk.indexOf(Data.pk) !== -1 ?
                deletePk.pk.splice(IndexPk, 1)
                :
                deletePk.pk.push(Data.pk)
        }
    }, [deletePk])

    const optionChange = useCallback(async (filter: number) => {
        setOption(filter)
        const tempUrl = `${API_URLS['order'].list}?keyword=${searchValue}&type=${filter}&page=${page.current}&limit=15`
        const res = await getCustomerData(tempUrl)

        const orderList = res.info_list.map((v) => {

            const quantity = v.quantity.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
            const unpaid = v.unpaid.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")


            return {...v, quantity: quantity, unpaid: unpaid}
        })

        setList(orderList)

        setPage({current: res.current_page, total: res.total_page})
    }, [option, searchValue])


    const searchChange = useCallback(async (search) => {
        setSearchValue(search)

    }, [searchValue])

    const searchOnClick = useCallback(async () => {

        const tempUrl = `${API_URLS['order'].list}?keyword=${searchValue}&type=${option}&page=${page.current}&limit=15`
        const res = await getCustomerData(tempUrl)

        const orderList = res.info_list.map((v) => {

            const quantity = v.quantity.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
            const unpaid = v.unpaid.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")


            return {...v, quantity: quantity, unpaid: unpaid}
        })

        setList(orderList)

        setPage({current: res.current_page, total: res.total_page})

    }, [searchValue, option])

    const onClick = useCallback((mold) => {
        if (mold.pk === selectPk) {
            setSelectPk(null)
            setSelectMaterial(null)
            setSelectValue(null)
        } else {
            setSelectPk(mold.pk)
            setSelectMaterial(mold.product)
            setSelectValue(mold)
            //TODO: api 
            getData(mold.pk)
        }

    }, [list, selectPk])


    const eventdummy = [
        {
            Name: '',
            Width: 60,
            Color: 'white',
            Link: (v) => history.push(`/outsourcing/order/register/${v.pk}`)
        },
        {
            buttonState: true,
            Width: 98,
            Link: (v) => v.status === '' ? getComplete(v.pk) : getCancel(v.pk)
        }
    ]

    const titleeventdummy = [
        {
            Name: '',
            Width: 90,
            Link: () => history.push('/outsourcing/order/register')
        },
        {
            Name: '',
            Link: () => postDelete()
        }
    ]

    const detaileventdummy = [
        {
            Name: '',
            Width: 90,
        },
    ]

    const postDelete = useCallback(async () => {
        if (deletePk.pk.length <= 0) {
            alert('   .')
            return
        }
        const tempUrl = `${API_URLS['order'].delete}`
        const res = await postOutsourcingDelete(tempUrl, deletePk)

        arrayDelete()
        getList()
    }, [deletePk])


    const getComplete = useCallback(async (pk) => {
        //TODO: 
        const tempUrl = `${API_URLS['order'].complete}`
        const res = await postMoldState(tempUrl, {pk: pk})

        setSelectPk(null)
        setSelectValue(null)
        getList()
    }, [selectPk, selectValue])

    const getCancel = useCallback(async (pk) => {
        //TODO: 
        const tempUrl = `${API_URLS['order'].cancel}`
        const res = await postMoldState(tempUrl, {pk: pk})

        setSelectPk(null)
        setSelectValue(null)
        getList()
    }, [selectPk, selectValue])

    const getData = useCallback(async (pk) => {
        //TODO: 
        const tempUrl = `${API_URLS['order'].load}?pk=${pk}`
        const res = await getOutsourcingList(tempUrl)

        setDetailList([res])
    }, [detailList])

    const getList = useCallback(async () => { // useCallback
        //TODO: 
        Notiflix.Loading.Circle();
        const tempUrl = `${API_URLS['order'].list}?keyword=${searchValue}&type=${option}&page=${page.current}&limit=15`
        const res = await getOutsourcingList(tempUrl)

        const orderList = res.info_list.map((v) => {

            const quantity = v.quantity.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
            const unpaid = v.unpaid.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
            const status = v.status === 'WAIT' ? '' : ''

            return {...v, quantity: quantity, unpaid: unpaid, status: status}
        })

        setList(orderList)

        setPage({current: res.current_page, total: res.total_page})
        Notiflix.Loading.Remove();
    }, [list, page])

    useEffect(() => {
        getList()
        setIndex(indexList['order'])
        // setList(dummy)
        // setDetailList(detaildummy)
        setEventList(eventdummy)
        setTitleEventList(titleeventdummy)
        setDetaileventList(detaileventdummy)
        setSubIndex(detailTitle['order'])
    }, [])

    useEffect(() => {
        getList()
    }, [page.current])

    return (
        <div>
            <OvertonTable
                title={'  '}
                titleOnClickEvent={titleEventList}
                allCheckOnClickEvent={allCheckOnClick}
                dropDownContents={contentsList}
                dropDownOption={option}
                dropDownOnClick={optionChange}
                searchBarChange={searchChange}
                searchButtonOnClick={searchOnClick}
                indexList={index}
                valueList={list}
                buttonState={true}
                EventList={eventList}
                currentPage={page.current}
                totalPage={page.total}
                pageOnClickEvent={(event, i) => setPage({...page, current: i})}
                checkOnClickEvent={checkOnClick}
                clickValue={selectValue}
                mainOnClickEvent={onClick}>
                {
                    selectPk !== null ?
                        <LineTable title={`${selectMaterial}  `} contentTitle={subIndex} contentList={detailList}>
                            <Line/>
                        </LineTable>
                        :
                        null
                }
            </OvertonTable>
        </div>
    )
}

const Line = Styled.hr`
    margin: 10px 20px 12px 0px;
    border-color: #353b48;
    height: 1px;
    background-color: #353b48;
`

export default OrderContainer
