import client from '../configs/common';

/**
 * postContractRegister()
 *  
 * @param {string} url  
 * @body {object}     
 * @returns {Boolean}    true/false 
 * @author 
 */
export const postContractRegister = async (url: string, bodyData: object) => {
    const temp: IServerData = await client.post(url, bodyData);
    if (temp.status === 400) {
        alert(' .')
        return
    }
    return temp
}

