import React, {useCallback, useEffect, useState} from 'react'
import {POINT_COLOR, TOKEN_NAME} from '../../Common/configset'
import Header from '../../Components/Text/Header'
import WhiteBoxContainer from '../../Containers/WhiteBoxContainer'
import NormalInput from '../../Components/Input/NormalInput'
import RegisterButton from '../../Components/Button/RegisterButton'
import NormalFileInput from '../../Components/Input/NormalFileInput'
import {getToken} from '../../Common/tokenFunctions'
import {getParameter, getRequest, postRequest} from '../../Common/requestFunctions'
import {uploadTempFile} from '../../Common/fileFuctuons'
import ListHeader from '../../Components/Text/ListHeader'
import OldFileInput from '../../Components/Input/OldFileInput'
import RadioInput from '../../Components/Input/RadioInput'
import NormalNumberInput from '../../Components/Input/NormalNumberInput'
import {useHistory} from 'react-router-dom'
import NormalAddressInput from '../../Components/Input/NormalAddressInput'
import useObjectInput from '../../Functions/UseInput'
import Styled from 'styled-components'
import {SF_ENDPOINT} from '../../Api/SF_endpoint'

//   
// ! isUpdate true     
const OutsourcingRegister = ({match}: any) => {
    const history = useHistory()

    const [pk, setPk] = useState<string>('')
    const [name, setName] = useState<string>('')
    const [no, setNo] = useState<number>()
    const [type, setType] = useState<string>('0') //0: , 1:
    const [phone, setPhone] = useState<string>('')
    const [address, setAddress] = useState<string>('')
    const [fax, setFax] = useState<string>('')
    const [phoneM, setPhoneM] = useState<string>('')
    const [emailM, setEmailM] = useState<string>('')
    const [email, setEmail] = useState<string>('')
    const [manager, setManager] = useState<string>('')
    const [ceo, setCeo] = useState<string>('')
    const [infoList, setInfoList] = useState<IInfo[]>([])

    const [paths, setPaths] = useState<any[1]>([null])
    const [oldPaths, setOldPaths] = useState<any[1]>([null])

    const [isUpdate, setIsUpdate] = useState<boolean>(false)

    const [inputData, setInputData] = useObjectInput('CHANGE', {
        name: '',
        description: '',
        location: {
            postcode: '',
            roadAddress: '',
            detail: '',
        },

    })


    useEffect(() => {
        if (match.params.pk) {
            ////alert(`   - pk :` + param)
            setIsUpdate(true)
            getData()
        }

    }, [])


    /**
     * addFiles()
     *  
     * @param {object(file)} event.target.files[0] 
     * @returns X
     */
    const addFiles = async (event: any, index: number): Promise<void> => {
        if (event.target.files[0] === undefined) {

            return
        }
        if (event.target.files[0].type.includes('image')) { // 

            const tempFile = event.target.files[0]
            const res = await uploadTempFile(event.target.files[0])

            if (res !== false) {
                const tempPatchList = paths.slice()
                tempPatchList[index] = res
                setPaths(tempPatchList)
                return
            } else {
                return
            }

        } else {

            //alert('   .')
        }

    }


    /**
     * getData()
     *     
     * @param {string} url  
     * @param {string} pk  pk
     * @returns X
     */
    const getData = useCallback(async () => {

        const res = await postRequest(`${SF_ENDPOINT}/api/v1/outsourcing/load`, {pk: match.params.pk}, getToken(TOKEN_NAME))

        if (res === false) {
            //TODO:  
        } else {
            if (res.status === 200) {
                const data = res.results
                setName(data.name)
                setPk(data.pk)
                setNo(data.number)
                setType(data.type)
                setPk(data.pk)
                setCeo(data.ceo_name)
                setPaths([data.photo_url === '-' ? null : data.photo_url])
                setPhone(data.telephone)
                setEmailM(data.manager_email)
                setPhoneM(data.manager_phone)
                setManager(data.manager)
                setEmail(data.ceo_email)

                setInfoList(data.info_list)
                setInputData('location', data.address)
                setFax(data.fax)

            } else {
                //TODO:   
            }
        }
    }, [pk, name, no, type, ceo, paths, oldPaths, phone, emailM, email, phone, phoneM, address, fax, inputData])

    /**
     * onsubmitFormUpdate()
     *    
     * @param {string} url  
     * @param {string} pk  pk
     * @param {string} name 
     * @param {string} no 
     * @param {object(file)} file  
     * @param {string} info 
     * @param {string} made 
     * @param {string} type 
     * @param {string} madeNo 
     * @returns X
     */
    const onsubmitFormUpdate = useCallback(async () => {

        if (name === '') {
            alert('  .  .')
            return
        } else if (ceo === '') {
            alert('  .  .')
            return
        } else if (no === null || no === undefined || no === 0 || no.toString() === '') {
            alert('   .  .')
            return
        }


        const data = {
            pk: match.params.pk,
            name: name,
            number: no.toString(),
            type: type.toString(),
            ceo_name: ceo,
            photo: paths[0],
            telephone: phone === '' ? null : phone,
            ceo_email: email === '' ? null : email,
            manager: manager === '' ? null : manager,
            manager_phone: phoneM === '' ? null : phoneM,
            manager_email: emailM === '' ? null : emailM,
            address: inputData.location.postcode === undefined && inputData.location.roadAddress === undefined && inputData.location.detail === undefined ? null : inputData.location,
            fax: fax === '' ? null : fax,
            //info_list : infoList.length > 0 ? JSON.stringify(infoList) : null,

        }

        const res = await postRequest(`${SF_ENDPOINT}/api/v1/outsourcing/update/`, data, getToken(TOKEN_NAME))

        if (res === false) {
            ////alert('      .')
        } else {
            if (res.status === 200) {
                //alert('  ')
                setIsUpdate(false)
                history.push('/outsourcing/current/list')
            } else {
                ////alert('      .')
            }
        }


    }, [pk, name, no, type, ceo, paths, oldPaths, phone, emailM, email, phone, phoneM, address, fax, manager, inputData])

    /**
     * onsubmitForm()
     *   
     * @param {string} url  
     * @param {string} name 
     * @param {string} no 
     * @param {string} info 
     * @param {string} made 
     * @param {string} type 
     * @param {string} madeNo 
     * @returns X
     */
    const onsubmitForm = useCallback(async () => {

        if (name === '') {
            alert('  .  .')
            return
        } else if (ceo === '') {
            alert('  .  .')
            return
        } else if (no === null || no === undefined || no === 0 || no.toString() === '') {
            alert('   .  .')
            return
        }

        const data = {

            name: name,
            number: no.toString(),
            type: type.toString(),
            ceo_name: ceo,
            photo: paths[0],
            telephone: phone === '' ? null : phone,
            ceo_email: email === '' ? null : email,
            manager: manager === '' ? null : manager,
            manager_phone: phoneM === '' ? null : phoneM,
            manager_email: emailM === '' ? null : emailM,
            address: inputData.location.postcode === '' && inputData.location.roadAddress === '' && inputData.location.detail === '' ? null : inputData.location,
            fax: fax === '' ? null : fax,
            // info_list : infoList.length > 0 ? JSON.stringify(infoList) : null,

        }


        const res = await postRequest(`${SF_ENDPOINT}/api/v1/outsourcing/register`, data, getToken(TOKEN_NAME))

        if (res === false) {
            //TODO:  
        } else {
            if (res.status === 200) {
                //alert('  ')

                history.push('/outsourcing/current/list')
            } else {
                //TODO:   
            }
        }

    }, [pk, name, no, type, ceo, paths, oldPaths, phone, emailM, email, phone, phoneM, address, fax, manager, inputData])


    return (
        <div>
            <Header title={isUpdate ? ' ' : ' '}/>
            <WhiteBoxContainer>
                <ListHeader title=" "/>
                <NormalInput title={' '} value={name} onChangeEvent={setName} description={'  '}/>
                <NormalInput title={' '} value={ceo} onChangeEvent={setCeo} description={'   '}/>
                <RadioInput title={' '} target={Number(type)} onChangeEvent={setType}
                            contents={[{value: 0, title: ''}, {value: 1, title: ''}]}/>
                <NormalNumberInput title={' '} value={no} onChangeEvent={setNo}
                                   description={'   (-)'}/>
                <br/>
                <ListHeader title=" "/>
                <NormalFileInput title={'  '} name={paths[0]} thisId={'photo'}
                                 onChangeEvent={(e) => addFiles(e, 0)}
                                 description={isUpdate ? paths[0] : '     '}/>
                {
                    isUpdate ?
                        <OldFileInput title={'  '} urlList={paths} nameList={['']} isImage={true}/>
                        :
                        null
                }
                <NormalAddressInput title={' '} value={inputData.location}
                                    onChangeEvent={(input) => setInputData(`location`, input)}/>
                <NormalInput title={'  '} value={phone} onChangeEvent={setPhone}
                             description={'    '}/>
                <NormalInput title={' '} value={email} onChangeEvent={setEmail}
                             description={'  '}/>
                <NormalInput title={'  FAX'} value={fax} onChangeEvent={setFax}
                             description={'  '}/>
                <NormalInput title={' '} value={manager} onChangeEvent={setManager}
                             description={' ()  '}/>
                <NormalInput title={' '} value={phoneM} onChangeEvent={setPhoneM}
                             description={' ()  '}/>
                <NormalInput title={' '} value={emailM} onChangeEvent={setEmailM}
                             description={' ()  '}/>
                {/*   
             <FullAddInput title={' '} onChangeEvent={()=>{
              const tempInfo = infoList.slice();
              tempInfo.push({title:`  ${infoList.length + 1}`, value:""});
              setInfoList(tempInfo)
            }}>
              {
                infoList.map((v: IInfo, i)=>{
                  return(
                      <CustomIndexInput index={i} value={v}
                      onRemoveEvent={()=>{
                        const tempInfo = infoList.slice();
                        tempInfo.splice(i, 1)
                        setInfoList(tempInfo)
                      }}
                      onChangeEvent={(obj: IInfo)=>{
                        const tempInfo = infoList.slice();
                        tempInfo.splice(i, 1, obj)
                        setInfoList(tempInfo)
                      }}
                      />
                  )
                })
              }
              </FullAddInput>
            */}
                {isUpdate ?
                    <div style={{marginTop: 40, marginLeft: 340}}>
                        <ButtonWrap onClick={async () => {
                            await onsubmitFormUpdate()
                        }}>
                            <div style={{width: 360, height: 40}}>
                                <p style={{fontSize: 18, marginTop: 15}}></p>
                            </div>
                        </ButtonWrap>
                    </div>
                    :
                    <div style={{marginTop: 40, marginLeft: 340}}>
                        <ButtonWrap onClick={async () => {
                            await onsubmitForm()
                        }}>
                            <div style={{width: 360, height: 40}}>
                                <p style={{fontSize: 18, marginTop: 15}}></p>
                            </div>
                        </ButtonWrap>
                    </div>
                }
            </WhiteBoxContainer>
        </div>
    )
}

const ButtonWrap = Styled.button`
    padding: 4px 12px 4px 12px;
    margin-bottom: 20px;
    border-radius: 5px;
    color: black;
    background-color: ${POINT_COLOR};
    border: none;
    font-weight: bold;
    font-size: 13px;
    img {
      margin-right: 7px;
      width: 14px;
      height: 14px;
    }
`

export default OutsourcingRegister
