import React, {useCallback, useEffect, useState} from 'react'
import {POINT_COLOR, TOKEN_NAME} from '../../Common/configset'
import Header from '../../Components/Text/Header'
import WhiteBoxContainer from '../../Containers/WhiteBoxContainer'
import NormalInput from '../../Components/Input/NormalInput'
import RegisterButton from '../../Components/Button/RegisterButton'
import {getToken} from '../../Common/tokenFunctions'
import {getParameter, getRequest, postRequest} from '../../Common/requestFunctions'
import {uploadTempFile} from '../../Common/fileFuctuons'
import ListHeader from '../../Components/Text/ListHeader'
import {useHistory} from 'react-router-dom'
import ColorCalendarDropdown from '../../Components/Dropdown/ColorCalendarDropdown'
import InputContainer from '../InputContainer'
import Styled from 'styled-components'
import ProductionPickerModal from '../../Components/Modal/ProductionPickerModal'
import NormalAddressInput from '../../Components/Input/NormalAddressInput'
import useObjectInput from '../../Functions/UseInput'
import OutsourcingPickerModal from '../../Components/Modal/OutsourcingRegister'
import NormalNumberInput from '../../Components/Input/NormalNumberInput'
import {SF_ENDPOINT} from "../../Api/SF_endpoint";

//   
// ! isUpdate true     
interface Props {
    match: any;
    // chilren: string;
}

const ContractRegister = ({match}: Props) => {
    const history = useHistory()

    const [selectDate, setSelectDate] = useState<string>('')
    const [pk, setPk] = useState<string>('')
    const [name, setName] = useState<string>('')
    const [no, setNo] = useState<number>()
    const [type, setType] = useState<number>(0) //0: , 1:
    const [phone, setPhone] = useState<string>('')
    const [address, setAddress] = useState<string>('')
    const [fax, setFax] = useState<string>('')
    const [phoneM, setPhoneM] = useState<string>('')
    const [emailM, setEmailM] = useState<string>('')
    const [email, setEmail] = useState<string>('')
    const [manager, setManager] = useState<string>('')
    const [ceo, setCeo] = useState<string>('')
    const [infoList, setInfoList] = useState<IInfo[]>([])

    const [paths, setPaths] = useState<any[1]>([null])
    const [oldPaths, setOldPaths] = useState<any[1]>([null])

    const [isUpdate, setIsUpdate] = useState<boolean>(false)

    const [selectMaterial, setSelectMaterial] = useState<{ name?: string, pk?: string }>()
    const [selectOutsource, setSelectOutsource] = useState<{ name?: string, pk?: string }>()
    const [quantity, setQuantity] = useState<number>()
    const [unpaid, setUnpaid] = useState<number>()
    const [paymentCondition, setPaymentCondition] = useState('')

    // 
    const [isPoupup, setIsPoupup] = useState<boolean>(false)
    const [isSearched, setIsSearched] = useState<boolean>(false)
    const [keyword, setKeyword] = useState<string>('')
    const [checkList, setCheckList] = useState<IMaterial[]>([])
    const [list, setList] = useState<IMaterial[]>([])
    const [searchList, setSearchList] = useState<IMaterial[]>([])

    const [inputData, setInputData] = useObjectInput('CHANGE', {
        name: '',
        description: '',
        location: {
            postcode: '',
            roadAddress: '',
            detail: '',
        },

    })

    useEffect(() => {
        if (match.params.pk) {
            ////alert(`   - pk :` + param)
            setIsUpdate(true)
            getData()
        }

    }, [])

    const onClickSearch = useCallback(async (e) => {
        ////alert('keyword')
        e.preventDefault()
        let type = 'material'
        // //alert('keyword')
        if (isPoupup === true) {
            type = 'material'
        } else {
            return
        }

        if (keyword === '' || keyword.length < 2) {
            //alert('2   ')

            return
        }
        setIsSearched(true)

        const res = await getRequest(`${SF_ENDPOINT}/api/v1/${type}/search?keyword=` + keyword, getToken(TOKEN_NAME))

        if (res === false) {
            //TODO:  
        } else {
            if (res.status === 200) {
                const results = res.results
                if (isPoupup === true) {
                    setSearchList(results)
                } else {
                    return
                }


            } else {
                //TODO:   
            }
        }
    }, [keyword])

    /**
     * addFiles()
     *  
     * @param {object(file)} event.target.files[0] 
     * @returns X
     */
    const addFiles = async (event: any, index: number): Promise<void> => {

        if (event.target.files[0] === undefined) {

            return
        }

        if (event.target.files[0].type.includes('image')) { // 

            const tempFile = event.target.files[0]

            const res = await uploadTempFile(event.target.files[0])

            if (res !== false) {
                const tempPatchList = paths.slice()
                tempPatchList[index] = res
                setPaths(tempPatchList)
                return
            } else {
                return
            }

        } else {

            //alert('   .')
        }

    }


    /**
     * getData()
     *     
     * @param {string} url  
     * @param {string} pk  pk
     * @returns X
     */
    const getData = useCallback(async () => {

        const res = await getRequest(`${SF_ENDPOINT}/api/v1/outsourcing/contract/load?pk=` + match.params.pk, getToken(TOKEN_NAME))

        if (res === false) {
            //TODO:  
        } else {
            if (res.status === 200) {
                const data = res.results
                setInputData('location', data.address)
                setSelectMaterial({name: data.product, pk: data.product_pk})
                setSelectOutsource({name: data.company_name, pk: data.company_pk})
                setQuantity(data.quantity)
                setUnpaid(data.unpaid)
                setSelectDate(data.due_date)
                setPaymentCondition(data.payment_condition)
            } else {
                //TODO:   
            }
        }
    }, [pk, selectOutsource, selectMaterial, selectDate, quantity, unpaid, paymentCondition, inputData])

    /**
     * onsubmitFormUpdate()
     *    
     * @param {string} url  
     * @param {string} pk  pk
     * @param {string} name 
     * @param {string} no 
     * @param {object(file)} file  
     * @param {string} info 
     * @param {string} made 
     * @param {string} type 
     * @param {string} madeNo 
     * @returns X
     */
    const onsubmitFormUpdate = useCallback(async () => {

        if (selectOutsource?.pk === '' || selectOutsource?.pk === undefined) {
            alert('  .  .')
            return
        } else if (selectMaterial?.pk === '' || selectMaterial?.pk === undefined) {
            alert('  .  .')
            return
        } else if (!quantity || quantity === 0) {
            alert('  .  .')
            return
        } else if (!unpaid) {
            alert('   .  .')
            return
        } else if (paymentCondition === '') {
            alert('   .  .')
            return
        } else if (selectDate === '') {
            alert('  .  .')
            return
        } else if (inputData.location.postcode === '') {
            alert('   .  .')
            return
        }

        const data = {
            pk: match.params.pk,
            company: selectOutsource?.pk,
            product: selectMaterial?.pk,
            quantity: quantity.toString(),
            unpaid: unpaid.toString(),
            due_date: selectDate,
            payment_condition: paymentCondition,
            address: inputData.location
            //info_list : infoList.length > 0 ? JSON.stringify(infoList) : null,
        }
        const res = await postRequest(`${SF_ENDPOINT}/api/v1/outsourcing/contract/update/`, data, getToken(TOKEN_NAME))

        if (res === false) {
            ////alert('      .')
        } else {
            if (res.status === 200) {
                //alert('  ')
                history.goBack()
            } else {
                ////alert('      .')
            }
        }


    }, [pk, selectOutsource, selectMaterial, selectDate, quantity, unpaid, paymentCondition, inputData])

    /**
     * onsubmitForm()
     *   
     * @param {string} url  
     * @param {string} name 
     * @param {string} no 
     * @param {string} info 
     * @param {string} made 
     * @param {string} type 
     * @param {string} madeNo 
     * @returns X
     */
    const onsubmitForm = useCallback(async () => {
        ////alert(JSON.stringify(infoList))
        if (selectOutsource?.pk === '' || selectOutsource?.pk === undefined) {
            alert('  .  .')
            return
        } else if (selectMaterial?.pk === '' || selectMaterial?.pk === undefined) {
            alert('  .  .')
            return
        } else if (quantity === null) {
            alert('  .  .')
            return
        } else if (unpaid === null) {
            alert('   .  .')
            return
        } else if (paymentCondition === '') {
            alert('   .  .')
            return
        } else if (selectDate === '') {
            alert('  .  .')
            return
        } else if (inputData.location.postcode === '') {
            alert('   .  .')
            return
        }
        const data = {
            company: selectOutsource?.pk,
            product: selectMaterial?.pk,
            quantity: String(quantity),
            unpaid: String(unpaid),
            due_date: selectDate,
            payment_condition: paymentCondition,
            address: inputData.location

        }

        const res = await postRequest(`${SF_ENDPOINT}/api/v1/outsourcing/contract/register`, data, getToken(TOKEN_NAME))

        if (res === false) {
            //TODO:  
        } else {
            if (res.status === 200) {

                history.push('/outsourcing/contract/list')
            } else {
                //TODO:   
            }
        }

    }, [selectOutsource, selectMaterial, selectDate, quantity, unpaid, paymentCondition, inputData])


    return (
        <div>
            <Header title={isUpdate ? ' ' : ' '}/>
            <WhiteBoxContainer>
                <ListHeader title=" "/>
                <InputContainer title={' '} width={120}>
                    <OutsourcingPickerModal select={selectOutsource}
                                            onClickEvent={(e) => {
                                                setSelectOutsource({...selectOutsource, ...e})
                                            }} text={'  .'}/>
                </InputContainer>
                <InputContainer title={'()'} width={120}>
                    <ProductionPickerModal select={selectMaterial}
                                           onClickEvent={(e) => {
                                               setSelectMaterial({...selectMaterial, ...e})
                                           }} text={' .'} type={1}/>
                </InputContainer>
                <NormalNumberInput title={''} value={quantity} onChangeEvent={setQuantity} description={' .'}
                                   width={120}/>
                <NormalNumberInput title={' '} value={unpaid} onChangeEvent={setUnpaid}
                                   description={'  .'}
                                   width={120}/>
                <NormalInput title={' '} value={paymentCondition} onChangeEvent={setPaymentCondition}
                             description={'   .'} width={120}/>
                <InputContainer title={''} width={120}>
                    <div style={{
                        display: 'flex',
                        flex: 1,
                        flexDirection: 'row',
                        backgroundColor: '#f4f6fa',
                        border: '0.5px solid #b3b3b3',
                        height: 32
                    }}>
                        <div style={{width: 817, display: 'table-cell'}}>
                            <div style={{marginTop: 5}}>
                                {
                                    selectDate === ''
                                        ? <InputText>&nbsp;   .</InputText>
                                        : <InputText style={{color: '#111319'}}>&nbsp; {selectDate}</InputText>
                                }
                            </div>
                        </div>
                        <ColorCalendarDropdown select={selectDate} onClickEvent={(select) => {
                            setSelectDate(select)
                        }} text={' '} type={'single'} customStyle={{height: 32, marginLeft: 0}}/>
                    </div>
                </InputContainer>
                <NormalAddressInput title={' '} value={inputData.location}
                                    onChangeEvent={(input) => setInputData(`location`, input)}/>
                {/*   
             <FullAddInput title={' '}  ={()=>{
              const tempInfo = infoList.slice()=-
              tempInfo.push({title:`  ${infoList.length + 1}`, value:""});
              setInfoList(tempInfo)
            }}>
              {
                infoList.map((v: IInfo, i)=>{
                  return(
                      <CustomIndexInput index={i} value={v}
                      onRemoveEvent={()=>{
                        const tempInfo = infoList.slice();
                        tempInfo.splice(i, 1)
                        setInfoList(tempInfo)
                      }}
                      onChangeEvent={(obj: IInfo)=>{
                        const tempInfo = infoList.slice();
                        tempInfo.splice(i, 1, obj)
                        setInfoList(tempInfo)
                      }}
                      />
                  )
                })
              }
              </FullAddInput>

            */}
                {isUpdate ?
                    <div style={{marginTop: 72, marginLeft: 340}}>
                        <ButtonWrap onClick={async () => {
                            await onsubmitFormUpdate()
                        }}>
                            <div style={{width: 360, height: 46, boxSizing: 'border-box', paddingTop: '9px'}}>
                                <p style={{fontSize: 18}}></p>
                            </div>
                        </ButtonWrap>
                    </div>
                    :
                    <div style={{marginTop: 72, marginLeft: 340}}>
                        <ButtonWrap onClick={async () => {
                            await onsubmitForm()
                        }}>
                            <div style={{width: 360, height: 46, boxSizing: 'border-box', paddingTop: '9px'}}>
                                <p style={{fontSize: 18}}></p>
                            </div>
                        </ButtonWrap>
                    </div>
                }
            </WhiteBoxContainer>
        </div>
    )
}

const InputText = Styled.p`
    color: #b3b3b3;
    font-size: 15px;
    text-align: left;
    vertical-align: middle;
    font-weight: regular;
`


const ButtonWrap = Styled.button`
    padding: 4px 12px 4px 12px;
    border-radius: 5px;
    color: black;
    background-color: ${POINT_COLOR};
    border: none;
    font-weight: bold;
    font-size: 13px;
    img {
      margin-right: 7px;
      width: 14px;
      height: 14px;
    }
`

export default ContractRegister
