import client from '../configs/basic'


/**
 * getProjectList()
 *    /    
 * @param {string} url  
 * @returns {Boolean}    true/false 
 * @author 
 */
export const getProjectList = async (url: string) => {
    const temp: IServerData = await client.get(url)
    if (temp.status === 400) {
        alert(' .')
        return
    }
    return temp.results
}


