import React, {useCallback, useEffect, useState} from 'react'
import Styled from 'styled-components'
import {BASE_URL, BG_COLOR_SUB2, TOKEN_NAME} from '../../Common/configset'
import DashboardWrapContainer from '../../Containers/DashboardWrapContainer'
import Header from '../../Components/Text/Header'
import WhiteBoxContainer from '../../Containers/WhiteBoxContainer'
import NormalInput from '../../Components/Input/NormalInput'
import RegisterButton from '../../Components/Button/RegisterButton'
import {getToken} from '../../Common/tokenFunctions'
import SearchModalContainer from '../../Containers/SearchModalContainer'
import AddInput from '../../Components/Input/AddInput'
import SearchInput from '../../Components/Input/SearchInput'
import AddList from '../../Components/List/AddList'
import TextList from '../../Components/List/TextList'
import InnerBodyContainer from '../../Containers/InnerBodyContainer'
import {getParameter, getRequest, postRequest} from '../../Common/requestFunctions'


//    
// ! isUpdate true     
const RegisterLine = () => {

    const [pk, setPk] = useState<string>('')
    const [no, setNo] = useState<string>('')
    const [info, setInfo] = useState<string>('')
    const [isUpdate, setIsUpdate] = useState<boolean>(false)

    //
    const [isPoupup, setIsPoupup] = useState<boolean>(false)
    const [isSearched, setIsSearched] = useState<boolean>(false)
    const [keyword, setKeyword] = useState<string>('')
    const [checkList, setCheckList] = useState<IMachine[]>([])
    const [list, setList] = useState<IMachine[]>([])
    const [searchList, setSearchList] = useState<IMachine[]>([])
    const [searchList2, setSearchList2] = useState<ISubMachine[]>([])
    const [isPoupup2, setIsPoupup2] = useState<boolean>(false)
    const [checkList2, setCheckList2] = useState<ISubMachine[]>([])
    const [list2, setList2] = useState<ISubMachine[]>([])


    useEffect(() => {
        //setSearchList(dataSet.machineList)
        //setSearchList2(dataSet.subMachineList)
        if (getParameter('pk') !== '') {
            setPk(getParameter('pk'))
            ////alert(`   - pk :` + param)
            setIsUpdate(true)
            getData()
        }


    }, [])

    /**
     * getData()
     *     
     * @param {string} url  
     * @param {string} pk  pk
     * @returns X
     */
    const getData = useCallback(async () => {

        const res = await getRequest(BASE_URL + '/api/v1/procedure/view/' + getParameter('pk'), getToken(TOKEN_NAME))

        if (res === false) {
            //TODO:  
        } else {
            if (res.status === 200) {
                const data = res.results
                setList(data.machines)
                setList2(data.peripherals)
                setPk(data.pk)
                setNo(data.line_code)
                setInfo(data.line_detail)
            } else {
                //TODO:   
            }
        }
    }, [pk, list, info, no, list2])


    /**
     * onClickSearch()
     *   
     * @param {string} url  
     * @param {string} keyword  
     * @returns X
     */
    const onClickSearch = useCallback(async (e) => {

        e.preventDefault()
        ////alert(' : keyword - ' + keyword);
        //return;
        if (keyword === '' || keyword.length < 1) {
            //alert('2   ')

            return
        }
        setIsSearched(true)

        const res = await getRequest('http://255.255.255.255:8299/api/v1/procedure/search?keyword=' + keyword, getToken(TOKEN_NAME))

        if (res === false) {
            //TODO:  
        } else {
            if (res.status === 200) {
                const data = res.results
                //setKeyword('')
                setSearchList(data)
            } else {
                //TODO:   
            }
        }
    }, [keyword, searchList, searchList2])

    /**
     * onClickSearch()
     *   
     * @param {string} url  
     * @param {string} keyword  
     * @returns X
     */
    const onClickSearch2 = useCallback(async (e) => {

        e.preventDefault()
        ////alert(' : keyword - ' + keyword);
        //return;
        if (keyword === '' || keyword.length < 1) {
            //alert('2   ')

            return
        }
        setIsSearched(true)

        const res = await getRequest('http://255.255.255.255:8299/api/v1/peripheral/search?keyword=' + keyword, getToken(TOKEN_NAME))

        if (res === false) {
            //TODO:  
        } else {
            if (res.status === 200) {
                const data = res.results
                //setKeyword('')
                setSearchList2(data)
            } else {
                //TODO:   
            }
        }
    }, [keyword, searchList, searchList2])

    /**
     * onClickCheck()
     *  
     * @param {string} id   pk
     * @returns X
     */
    const onClickCheck = useCallback((id: string) => {

        /*
        console.log(' : ' + checkList.length + ' / ' +id)
        let tempArray = checkList.filter((v)=>(v !== id));
        if(checkList.indexOf(id) !== -1){
          setCheckList(tempArray);
        }else{
          if(checkList.length === 0){
            let tempArray = new Array();
            tempArray.push(id);
            setCheckList(tempArray);
          }else{
            let tempArray = checkList.map((v)=>(v));
            tempArray.push(id);
            setCheckList(tempArray);
          }
        }
        console.log(' : ' + tempArray);
        */
    }, [checkList])

    const onsubmitForm = useCallback(async (e) => {
        e.preventDefault()

        if (no === '') {
            //alert("   .  .")
            return
        }

        let pks = new Array()
        let pks2 = new Array()
        list.forEach((v: IMachine, i) => {
            pks[i] = v.pk
        })
        list2.forEach((v: ISubMachine, i) => {
            pks2[i] = v.pk
        })
        const data = {
            procedure_code: no,
            procedure_detail: info,
            machines: pks,
            peripherals: pks2,

        }

        const res = await postRequest(BASE_URL + '/api/v1/procedure/register', data, getToken(TOKEN_NAME))

        if (res === false) {
            //alert('.   .')
        } else {
            if (res.status === 200) {
                //alert('  ')
                setPk('')
                setList([])
                setList2([])
                setNo('')
                setInfo('')

            } else {
                //alert('.   .')
            }
        }


    }, [no, info, list, list2])

    return (
        <DashboardWrapContainer>


            <InnerBodyContainer>
                <Header title={isUpdate ? ' ' : ' '}/>
                <WhiteBoxContainer>
                    <form onSubmit={onsubmitForm}>
                        <NormalInput title={' '} value={no} onChangeEvent={setNo} description={'  '}/>
                        <NormalInput title={' '} value={info} onChangeEvent={setInfo}
                                     description={'    '}/>
                        {/*    +  */}
                        <AddInput title={' '} icType="solo" onChangeEvent={() => {
                            setIsPoupup(true)
                            setCheckList(list)
                            setKeyword('')
                        }
                        }>
                            {
                                list.map((v, i) => {
                                    return (
                                        <TextList key={i} onClickEvent={() => {
                                            const tempList = list.slice()
                                            const idx = list.indexOf(v)
                                            tempList.splice(idx, 1)
                                            setList(tempList)
                                        }}
                                                  title={''} name={v.machine_name}/>
                                    )
                                })
                            }
                        </AddInput>
                        {/*    +  */}
                        <AddInput title={' '} icType="solo" onChangeEvent={() => {
                            setIsPoupup2(true)
                            setCheckList2(list2)
                            setKeyword('')
                        }
                        }>
                            {
                                list2.map((v, i) => {
                                    return (
                                        <TextList key={i} onClickEvent={() => {
                                            const tempList = list2.slice()
                                            const idx = list2.indexOf(v)
                                            tempList.splice(idx, 1)
                                            setList2(tempList)
                                        }}
                                                  title={v.device_code} name={v.device_name}/>
                                    )
                                })
                            }
                        </AddInput>
                        <RegisterButton name={isUpdate ? '' : ''}/>

                    </form>
                </WhiteBoxContainer>

                {/*  */}
                <SearchModalContainer
                    onClickEvent={ //  
                        () => {
                            setIsPoupup(false)
                            setList(checkList)
                            setSearchList([])
                            setKeyword('')
                        }
                    }
                    isVisible={isPoupup} onClickClose={() => {
                    setIsPoupup(false)
                    setSearchList([])
                }} title={' '}>
                    <SearchInput description={' '} value={keyword}
                                 onChangeEvent={(e) => setKeyword(e.target.value)}
                                 onClickEvent={onClickSearch}/>
                    <div style={{width: '100%', marginTop: 20}}>
                        {
                            isSearched ?
                                searchList.map((v: IMachine, i) => {
                                    return (
                                        !v.is_registered ? //    
                                            <AddList key={i} pk={''} dim={false}
                                                     selected={checkList.find((k) => k.pk === v.pk) ? true : false}
                                                     onClickEvent={() => {
                                                         const tempList = checkList.slice()
                                                         if (checkList.find((k, index) => k.pk === v.pk)) {
                                                             const idx = checkList.indexOf(v)
                                                             tempList.splice(idx, 1)
                                                             setCheckList(tempList)
                                                         } else {
                                                             tempList.splice(0, 0, v)
                                                             setCheckList(tempList)
                                                         }
                                                     }}
                                                     title={''} name={v.machine_name}
                                            />
                                            :
                                            <AddList key={i} pk={''} dim={true} selected={false} onClickEvent={() => {
                                            }} title={v.pk} name={v.machine_name}/>
                                    )
                                })
                                :
                                null
                        }
                    </div>
                </SearchModalContainer>


                {/*   */}
                <SearchModalContainer
                    onClickEvent={ //  
                        () => {
                            setIsPoupup2(false)
                            setList2(checkList2)
                            setSearchList2([])
                            setKeyword('')
                        }
                    }
                    isVisible={isPoupup2} onClickClose={() => {
                    setIsPoupup2(false)
                    setSearchList2([])
                }} title={' '}>
                    <SearchInput description={' '} value={keyword}
                                 onChangeEvent={(e) => setKeyword(e.target.value)}
                                 onClickEvent={onClickSearch2}/>
                    <div style={{width: '100%', marginTop: 20}}>
                        {
                            isSearched ?
                                searchList2.map((v: ISubMachine, i) => {
                                    return (
                                        !v.is_registered ? //    
                                            <AddList key={i} pk={v.pk} dim={false}
                                                     selected={checkList2.find((k) => k.pk === v.pk) ? true : false}
                                                     onClickEvent={() => {
                                                         const tempList = checkList2.slice()
                                                         if (checkList2.find((k, index) => k.pk === v.pk)) {
                                                             const idx = checkList2.indexOf(v)
                                                             tempList.splice(idx, 1)
                                                             setCheckList2(tempList)
                                                         } else {
                                                             tempList.splice(0, 0, v)
                                                             setCheckList2(tempList)
                                                         }
                                                     }}
                                                     title={v.device_code} name={v.device_name}
                                            />
                                            :
                                            <AddList key={i} pk={v.pk} dim={true} selected={false} onClickEvent={() => {
                                            }} title={v.device_code} name={v.device_name}/>
                                    )
                                })
                                :
                                null
                        }
                    </div>
                </SearchModalContainer>

            </InnerBodyContainer>

        </DashboardWrapContainer>

    )
}
const FullPageDiv = Styled.div`
  width: 100%;
  height: 100%;
  color: white;
  background-color: ${BG_COLOR_SUB2}
`


export default RegisterLine
