import React, {useCallback, useEffect, useState} from 'react'
import {TOKEN_NAME} from '../../Common/configset'
import DashboardWrapContainer from '../../Containers/DashboardWrapContainer'
import Header from '../../Components/Text/Header'
import WhiteBoxContainer from '../../Containers/WhiteBoxContainer'
import NormalInput from '../../Components/Input/NormalInput'
import RegisterButton from '../../Components/Button/RegisterButton'
import NormalFileInput from '../../Components/Input/NormalFileInput'
import {getToken} from '../../Common/tokenFunctions'
import SubNavigation from '../../Components/Navigation/SubNavigation'
import {ROUTER_MENU_LIST} from '../../Common/routerset'
import InnerBodyContainer from '../../Containers/InnerBodyContainer'
import {getParameter, getRequest, postRequest} from '../../Common/requestFunctions'
import AddInput from '../../Components/Input/AddInput'
import NormalNumberInput from '../../Components/Input/NormalNumberInput'
import TextList from '../../Components/List/TextList'
import SearchModalContainer from '../../Containers/SearchModalContainer'
import SearchInput from '../../Components/Input/SearchInput'
import SearchedList from '../../Components/List/SearchedList'
import {uploadTempFile} from '../../Common/fileFuctuons'
import OldFileInput from '../../Components/Input/OldFileInput'
import moment from 'moment'
import DateInput from '../../Components/Input/DateInput'

interface IInfo {
    title: string,
    value: string,
}

//    
const RegisterInferior = () => {

    const [pk, setPk] = useState<string>('')
    const [name, setName] = useState<string>('')
    const [amount, setAmount] = useState<number>(0)
    const [isUpdate, setIsUpdate] = useState<boolean>(false)
    const [code, setCode] = useState<string>('')
    const [description, setDescription] = useState<string>('')
    const [date, setDate] = useState<string>(moment().format('YYYY-MM-DD'))

    //
    const [isPoupup, setIsPoupup] = useState<boolean>(false)
    const [isSearched, setIsSearched] = useState<boolean>(false)
    const [keyword, setKeyword] = useState<string>('')


    const [list, setList] = useState<ISearchedList[]>([])
    const [checkList, setCheckList] = useState<ISearchedList[]>([])
    const [searchList, setSearchList] = useState<ISearchedList[]>([])

    const [isPoupup2, setIsPoupup2] = useState<boolean>(false)
    const [list2, setList2] = useState<ISearchedList[]>([])
    const [checkList2, setCheckList2] = useState<ISearchedList[]>([])
    const [searchList2, setSearchList2] = useState<ISearchedList[]>([])

    const [paths, setPaths] = useState<any[1]>([null])
    const [oldPaths, setOldPaths] = useState<any[1]>([null])


    useEffect(() => {
        //setIsSearched(true)
        //setSearchList(dataSet.materialList);
        //setSearchList3(dataSet.machineList);

        if (getParameter('pk') !== '') {
            setPk(getParameter('pk'))
            ////alert(`   - pk :` + param)
            setIsUpdate(true)
            //getData()
        }

    }, [])

    /**
     * getData()
     *  
     * @param {string} url  
     * @param {string} pk  pk
     * @returns X
     */
    const getData = useCallback(async () => {

        const res = await getRequest('http://255.255.255.255:8299/api/v1/process/view?pk=' + getParameter('pk'), getToken(TOKEN_NAME))

        if (res === false) {
            //TODO:  
        } else {


            if (res.status === 200) {

                const data = res.results
                setName(data.name)
                setList(new Array(data.material))
                //setList(new Array(data.s))

            } else if (res.status === 1001 || res.data.status === 1002) {
                //TODO:    
            } else {
                //TODO:   
            }
        }
    }, [pk, name, list, list2])

    /**
     * onsubmitForm()
     *  
     * @param {string} url  
     * @param {string} name 
     * @param {string} mold  pk
     * @param {string} material  pk
     * @param {string} output  pk
     * @param {string} machine  pk
     * @returns X
     */
    const onsubmitForm = useCallback(async (e) => {
        e.preventDefault()
        //TODO: 

        ////alert(' :  - ' + amount + code + name + info + made + spec + info );
        //return;
        const data = {
            pk: list[0].pk,
            date: date,
            amount: amount,
            reason: 2,
            description: description,
            photo: paths[0],


        }

        const res = await postRequest('http://255.255.255.255:8299/api/v1/stock/out', data, getToken(TOKEN_NAME))

        if (res === false) {
            //TODO:  
        } else {
            if (res.status === 200) {
                //alert('  ')
                setName('')
                setList([])
                setList2([])
                setAmount(0)
                setDescription('')
                setPaths([null])

            } else if (res.status === 1000) {
                //alert('  .')
            } else {
                //alert(' .   .')
                //TODO:   
            }
        }

    }, [pk, list, list2, paths, amount, description, paths])


    /**
     * onsubmitFormUpdate()
     *   
     * @param {string} url  
     * @param {string} pk pk
     * @param {string} name 
     * @param {array} mold  pk
     * @param {string} material  pk
     * @param {string} output  pk
     * @param {string} machine  pk
     * @returns X
     */
    const onsubmitFormUpdate = useCallback(async (e) => {
        e.preventDefault()

        if (list.length < 1 || list2.length < 1) {
            //alert(',    . ')
            return
        }
        ////alert(' :  - ' + amount + code + name + info + made + spec + info );
        //return;
        const data = {
            pk: getParameter('pk'),
            material_pk: list[0].pk,
            barcode_pk: list2[0].pk,
            code: code,
            photo: paths[0],
            description: description,

        }
        const res = await postRequest('http://255.255.255.255:8299/api/v1/barcode/product/update' + getParameter('pk'), data, getToken(TOKEN_NAME))

        if (res === false) {
            //TODO:  
        } else {
            if (res.status === 200) {
                //alert('  ')
            } else {
                //TODO:   
            }
        }

    }, [pk, name, list, list2, paths, code, description])

    /**
     * onClickSearch()
     *   
     * @param {string} url  
     * @param {string} keyword  
     * @returns X
     */
    const onClickSearch = useCallback(async (e) => {

        e.preventDefault()
        let type = 'material'

        if (isPoupup === true) {
            type = 'material'
        } else if (isPoupup2 === true) {
            type = 'barcode'
        } else {
            return
        }

        if (keyword === '' || keyword.length < 2) {
            //alert('2   ')

            return
        }
        setIsSearched(true)

        const res = await getRequest(`http://255.255.255.255:8299/api/v1/common/search?keyword=${keyword}&type=${type}&orderBy=1`, getToken(TOKEN_NAME))

        if (res === false) {
            //TODO:  
        } else {
            if (res.status === 200) {
                const results = res.results
                if (isPoupup === true) {
                    setSearchList(results)
                } else if (isPoupup2 === true) {
                    setSearchList2(results)
                }


            } else {
                //TODO:   
            }
        }
    }, [keyword])


    /**
     * addFiles()
     *  
     * @param {object(file)} event.target.files[0] 
     * @returns X
     */
    const addFiles = async (event: any, index: number): Promise<void> => {
        if (event.target.files[0] === undefined) {

            return
        }
        if (event.target.files[0].type.includes('image')) { // 

            const tempFile = event.target.files[0]
            const res = await uploadTempFile(event.target.files[0])

            if (res !== false) {
                const tempPatchList = paths.slice()
                tempPatchList[index] = res
                setPaths(tempPatchList)
                return
            } else {
                return
            }

        } else {

            //alert('   .')
        }

    }


    return (
        <DashboardWrapContainer index={9}>
            <SubNavigation list={ROUTER_MENU_LIST[9]}/>
            <InnerBodyContainer>
                <Header title={isUpdate ? ' ' : ' '}/>
                <WhiteBoxContainer>
                    <form onSubmit={isUpdate ? onsubmitFormUpdate : onsubmitForm}>
                        {/*    +   */}
                        <AddInput title={'  '} icType="solo" onlyOne={list.length > 0 ? true : false}
                                  onChangeEvent={() => {
                                      setIsPoupup(true)
                                      setCheckList(list)
                                      setKeyword('')
                                  }
                                  }>
                            {
                                list.map((v: ISearchedList, i) => {
                                    return (
                                        <TextList key={i}
                                                  onClickSearch={() => {
                                                      setIsPoupup(true)
                                                      setKeyword('')
                                                      setIsSearched(false)
                                                  }}
                                                  onClickEvent={() => {
                                                      setList([])
                                                  }}
                                                  title={v.code !== undefined ? v.code : ''} name={v.name}/>
                                    )
                                })
                            }
                        </AddInput>

                        <NormalNumberInput title={' '} value={amount} onChangeEvent={setAmount}
                                           description={'   '}/>

                        <DateInput title={' '} description={''} value={date} onChangeEvent={setDate}/>

                        <NormalInput title={' '} value={description} onChangeEvent={setDescription}
                                     description={'     '}/>

                        <NormalFileInput title={' '} name={paths[0]} thisId={'machinePhoto0'}
                                         onChangeEvent={(e) => addFiles(e, 0)}
                                         description={isUpdate ? oldPaths[0] : '   ()'}/>

                        {
                            isUpdate ?
                                <OldFileInput title={'  '} urlList={oldPaths} nameList={['']} isImage={true}/>

                                :
                                null
                        }

                        <RegisterButton name={isUpdate ? '' : ''}/>
                    </form>
                </WhiteBoxContainer>

                {/* -  */}
                <SearchModalContainer
                    onClickEvent={ //  
                        () => {
                            setIsPoupup(false)
                            setList(checkList)
                            setKeyword('')
                        }
                    }
                    isVisible={isPoupup} onClickClose={() => {
                    setIsPoupup(false)
                    setKeyword('')
                    setSearchList([])
                    setIsSearched(false)
                }} title={'() '}>
                    <SearchInput description={' '} value={keyword}
                                 onChangeEvent={(e) => setKeyword(e.target.value)}
                                 onClickEvent={onClickSearch}/>
                    <div style={{width: '100%', marginTop: 20}}>
                        {
                            isSearched ?
                                searchList.map((v: ISearchedList, i) => {
                                    return (

                                        <SearchedList key={i} pk={v.pk} widths={['52%', '52%']}
                                                      contents={[v.name, v.code !== undefined ? v.code : '']}
                                                      isIconDimmed={false}
                                                      isSelected={checkList.find((k) => k.pk === v.pk) ? true : false}
                                                      onClickEvent={() => {
                                                          const tempList = checkList.slice()
                                                          tempList.splice(0, 1, v)
                                                          setCheckList(tempList)
                                                      }}
                                        />

                                    )
                                })
                                :
                                null
                        }
                    </div>
                </SearchModalContainer>


            </InnerBodyContainer>
        </DashboardWrapContainer>

    )
}
export default RegisterInferior
