import React, {useCallback, useEffect, useState} from 'react';
import {TOKEN_NAME} from '../../Common/configset'
import DashboardWrapContainer from '../../Containers/DashboardWrapContainer';
import Header from '../../Components/Text/Header';
import {getToken} from '../../Common/tokenFunctions';
import 'react-dropdown/style.css'
import BasicDropdown from '../../Components/Dropdown/BasicDropdown';
import {getRequest} from '../../Common/requestFunctions';
import InnerBodyContainer from '../../Containers/InnerBodyContainer';

import StatusTable from '../../Components/Table/StatusTable';
import SmallButtonLink from '../../Components/Button/SmallButtonLink';
import {useUser} from '../../Context/UserContext';
import {SF_ENDPOINT} from "../../Api/SF_endpoint";

//    
const StatusList = () => {


    const [list, setList] = useState<IStatus[]>([]);
    const [option, setOption] = useState(0);
    const me = useUser();
    const optionList = [
        "", "", "",
    ]
    const index = {
        status: '',
        name: '',
        code: '',
        manufacturer: '',
        manufacturer_code: ' ',
    }


    /**
     * onClickFilter()
     *   
     * @param {string} filter  
     * @returns X
     */
    const onClickFilter = useCallback(async (filter: number) => {
        setOption(filter)
        ////alert(`  :  - filter : ${filter}` )
        //return;
        const results = await getRequest(`${SF_ENDPOINT}/api/v1/status/list/` + filter, getToken(TOKEN_NAME))

        if (results === false) {
            //alert('8087  :     .')
        } else {
            if (results.status === 200) {
                setList(results.results)
            } else {
                //alert('8087  :     .')
            }
        }
    }, [option, list])

    useEffect(() => {
        onClickFilter(0)

    }, [])

    const onClickModify = useCallback((id, type) => {
        if (type !== 'submachine') {
            window.location.href = `/update/machine?pk=${id}`
        } else { //peripheral
            window.location.href = `/update/submachine?pk=${id}`
        }

    }, [])


    return (
        <DashboardWrapContainer>
            <InnerBodyContainer>
                <div style={{position: 'relative'}}>
                    <Header title={'   '}/>
                    <div style={{position: 'absolute', display: 'inline-block', top: 0, right: 0, zIndex: 4}}>
                        {
                            me.is_admin ?
                                <>

                                    <SmallButtonLink name="+  " link="/register/machine"/>
                                    <SmallButtonLink name="+  " link="/register/submachine"/>
                                </>
                                :
                                null}
                        <BasicDropdown select={optionList[option]} contents={optionList} onClickEvent={onClickFilter}/>
                    </div>
                </div>

                <StatusTable widthList={['100px', '140px', '240px', '140px', '140px']} indexList={index} keyName={'pk'}
                             buttonName='' contents={list} onClickEvent={me.is_admin ? onClickModify : null}/>

            </InnerBodyContainer>
        </DashboardWrapContainer>

    );
}

export default StatusList;
