import React, {useCallback, useEffect, useState} from 'react'
import {POINT_COLOR, TOKEN_NAME} from '../../Common/configset'
import Header from '../../Components/Text/Header'
import WhiteBoxContainer from '../../Containers/WhiteBoxContainer'
import NormalInput from '../../Components/Input/NormalInput'
import RegisterButton from '../../Components/Button/RegisterButton'
import NormalFileInput from '../../Components/Input/NormalFileInput'
import {getToken} from '../../Common/tokenFunctions'
import {getParameter, postRequest} from '../../Common/requestFunctions'
import {uploadTempFile} from '../../Common/fileFuctuons'
import ListHeader from '../../Components/Text/ListHeader'
import OldFileInput from '../../Components/Input/OldFileInput'
import RadioInput from '../../Components/Input/RadioInput'
import NormalNumberInput from '../../Components/Input/NormalNumberInput'
import {useHistory} from 'react-router-dom'
import {API_URLS, getCustomerData} from '../../Api/mes/customer'
import NormalAddressInput from '../../Components/Input/NormalAddressInput'
import Styled from 'styled-components'
import {SF_ENDPOINT} from '../../Api/SF_endpoint'

interface Props {
  match: any;
  // chilren: string;
}


//   
// ! isUpdate true     
const CustomerRegister = ({match}: Props) => {


  const history = useHistory()

  const [pk, setPk] = useState<string>('')
  const [name, setName] = useState<string>('')
  const [no, setNo] = useState<number>()
  const [type, setType] = useState<string>('0') //0: , 1:
  const [phone, setPhone] = useState<number>()
  const [address, setAddress] = useState<{ postcode: string, roadAddress: string, detail: string }>(
    {postcode: '', roadAddress: '', detail: ''}
  )
  const [fax, setFax] = useState<number>()
  const [phoneM, setPhoneM] = useState<number>()
  const [emailM, setEmailM] = useState<string>('')
  const [email, setEmail] = useState<string>('')
  const [manager, setManager] = useState<string>('')
  const [ceo, setCeo] = useState<string>('')
  const [infoList, setInfoList] = useState<IInfo[]>([])

  const [paths, setPaths] = useState<any[1]>([null])
  const [oldPaths, setOldPaths] = useState<any[1]>([null])

  const [isUpdate, setIsUpdate] = useState<boolean>(false)


  useEffect(() => {
    if (match.params.pk) {
      ////alert(`   - pk :` + param)
      setIsUpdate(true)
      getData()
    }

  }, [])


  /**
   * addFiles()
   *  
   * @param {object(file)} event.target.files[0] 
   * @returns X
   */
  const addFiles = async (event: any, index: number): Promise<void> => {
    if (event.target.files[0] === undefined) {

      return
    }
    if (event.target.files[0].type.includes('image')) { // 

      const tempFile = event.target.files[0]
      const res = await uploadTempFile(event.target.files[0])

      if (res !== false) {
        const tempPatchList = paths.slice()
        tempPatchList[index] = res
        setPaths(tempPatchList)
        return
      } else {
        return
      }

    } else {

      //alert('   .')
    }

  }


  /**
   * getData()
   *     
   * @param {string} url  
   * @param {string} pk  pk
   * @returns X
   */
  const getData = useCallback(async () => {

    const tempUrl = `${API_URLS['customer'].load}?pk=${match.params.pk}`
    const res = await getCustomerData(tempUrl)

    if (res === false) {
      //TODO:  
    } else {
      setName(res.name)
      setPk(res.pk)
      setNo(res.number)
      setType(res.type)
      setPk(res.pk)
      setCeo(res.ceo_name)
      setOldPaths([res.photo])
      setPhone(res.telephone)
      setEmailM(res.manager_email)
      setPhoneM(res.manager_phone)
      setManager(res.manager)
      setEmail(res.ceo_email)

      setInfoList(res.info_list)
      setAddress(res.address ? res.address : {roadAddress: '', detail: '', postcode: ''})
      setFax(res.fax)
    }
  }, [pk, name, no, type, ceo, paths, oldPaths, phone, emailM, email, phone, phoneM, address, fax])

  /**
   * onsubmitFormUpdate()
   *    
   * @param {string} url  
   * @param {string} pk  pk
   * @param {string} name 
   * @param {string} no 
   * @param {object(file)} file  
   * @param {string} info 
   * @param {string} made 
   * @param {string} type 
   * @param {string} madeNo 
   * @returns X
   */
  const onsubmitFormUpdate = useCallback(async () => {

    if (name === '') {
      alert('  .  .')
      return
    } else if (ceo === '') {
      alert('   .  .')
      return
    } else if (!no && String(no) === '') {
      alert('   .  .')
      return
    }

    const data = {
      pk: pk,
      name: name.trim(),
      number: no,
      type: String(type),
      ceo_name: ceo.trim(),
      photo: paths[0],
      telephone: phone === undefined ? null : phone,
      ceo_email: (email === '' || !email) ? null : email.trim(),
      manager: (manager === '' || !manager) ? null : manager.trim(),
      manager_phone: String(phoneM) === '' ? null : String(phoneM),
      manager_email: (emailM === '' || !emailM) ? null : emailM.trim(),
      address: address ? address : null,
      fax: String(fax) === '' ? null : fax,
      //info_list : infoList.length > 0 ? JSON.stringify(infoList) : null,

    }

    const res = await postRequest(`${SF_ENDPOINT}/api/v1/customer/update/`, data, getToken(TOKEN_NAME))
    if (res === false) {
      // alert('      .')
    } else {
      // alert('  ')
      setIsUpdate(false)
      history.push('/customer/current/list')
    }

  }, [pk, name, no, type, ceo, paths, oldPaths, phone, emailM, email, phone, phoneM, address, fax, manager])

  const ckBisNo = (bisNo: string) => {
    //        10  .
    // @ts-ignore
    if ((bisNo = (bisNo + '').match(/\d{1}/g)).length != 10) {
      alert('   .')
      return false
    }
    //  / 
    let sum = 0
    const key = [1, 3, 7, 1, 3, 7, 1, 3, 5]
    // 0 ~ 8  9    .
    for(let i = 0; i < 9; i++) {
      sum += (key[i] * Number(bisNo[i]))
    }
    //  8    10     .
    //  10     10       .
    return (10 - ((sum + Math.floor(key[8] * Number(bisNo[8]) / 10)) % 10)) == Number(bisNo[9])
  }


  /**
   * onsubmitForm()
   *   
   * @param {string} url  
   * @param {string} name 
   * @param {string} no 
   * @param {string} info 
   * @param {string} made 
   * @param {string} type 
   * @param {string} madeNo 
   * @returns X
   */
  const onsubmitForm = useCallback(async () => {
    ////alert(JSON.stringify(infoList))
    console.log(no)
    if (name.trim() === '') {
      alert('  .  .')
      return
    } else if (ceo.trim() === '') {
      alert('   .  .')
      return
    } else if (!ckBisNo(String(no))) {
      // alert('   .  .')
      return
    }

    const data = {

      name: name.trim(),
      number: no,
      type: type,
      ceo_name: ceo.trim(),
      photo: paths[0],
      telephone: phone === undefined ? '' : phone,
      ceo_email: (email === '' || !email) ? '' : email.trim(),
      manager: (manager === '' || !manager) ? '' : manager.trim(),
      manager_phone: phoneM ? String(phoneM) === '' ? '' : String(phoneM) : '',
      manager_email: (emailM === '' || !emailM) ? '' : emailM.trim(),
      address: address ? address : '',
      fax: String(fax) === '' ? '' : fax,
      // info_list : infoList.length > 0 ? JSON.stringify(infoList) : null,

    }

    const res = await postRequest(`${SF_ENDPOINT}/api/v1/customer/register`, data, getToken(TOKEN_NAME))

    if (res === false) {
      //TODO:  
    } else {
      if (res.status === 200) {
        // alert('  ')

        history.push('/customer/current/list')
      } else {
        //TODO:   
      }
    }

  }, [pk, name, no, type, ceo, paths, oldPaths, phone, emailM, email, phone, phoneM, address, fax, manager])


  return (
    <div>
      <Header title={isUpdate ? ' ' : ' '}/>
      <WhiteBoxContainer>
        <ListHeader title=" "/>
        <NormalInput title={' '} value={name} onChangeEvent={setName} description={'  '}/>
        <NormalInput title={' '} value={ceo} onChangeEvent={setCeo} description={'   '}/>
        <RadioInput title={' '} target={Number(type)} onChangeEvent={setType}
                    contents={[{value: 0, title: ''}, {value: 1, title: ''}]}/>

        {/* <NormalInput title={' '} value={no} onChangeEvent={setNo} description={'   (-)'}/> */}
        <NormalNumberInput title={' '} value={no} onChangeEvent={setNo} returnType={'string'}
                           description={'   (-)'}/>
        <br/>
        <ListHeader title=" "/>
        <NormalFileInput title={'  '} name={paths[0]} thisId={'photo'}
                         onChangeEvent={(e) => addFiles(e, 0)}
                         description={isUpdate ? oldPaths[0] : '     '}
                         style={{width: 'calc(100% - 124px)'}}/>
        {
          isUpdate ?
            <OldFileInput title={'  '} urlList={oldPaths} nameList={['']} isImage={true}/>
            :
            null
        }
        <NormalNumberInput title={'  '} value={phone} onChangeEvent={setPhone} returnType={'string'}
                           description={'   '}/>
        <NormalAddressInput title={' '} value={address} onChangeEvent={(input) => setAddress(input)}/>
        <NormalInput title={' '} value={email} onChangeEvent={setEmail}
                     description={'  '}/>
        {/* <NormalInput title={'  FAX'} value={fax} onChangeEvent={setFax}
                             description={'  '}/> */}
        <NormalNumberInput title={'  FAX'} value={fax} returnType={'string'}
                           onChangeEvent={setFax}
                           description={'  '}/>
        <NormalInput title={' '} value={manager} onChangeEvent={setManager}
                     description={' ()  '}/>
        <NormalNumberInput title={' '} value={phoneM} onChangeEvent={setPhoneM} returnType={'string'}
                           description={' ()  '}/>
        <NormalInput title={' '} value={emailM} onChangeEvent={setEmailM}
                     description={' ()  '}/>
        {/*   
             <FullAddInput title={' '} onChangeEvent={()=>{
              const tempInfo = infoList.slice();
              tempInfo.push({title:`  ${infoList.length + 1}`, value:""});
              setInfoList(tempInfo)
            }}>
              {
                infoList.map((v: IInfo, i)=>{
                  return(
                      <CustomIndexInput index={i} value={v}
                      onRemoveEvent={()=>{
                        const tempInfo = infoList.slice();
                        tempInfo.splice(i, 1)
                        setInfoList(tempInfo)
                      }}
                      onChangeEvent={(obj: IInfo)=>{
                        const tempInfo = infoList.slice();
                        tempInfo.splice(i, 1, obj)
                        setInfoList(tempInfo)
                      }}
                      />
                  )
                })
              }
              </FullAddInput>
            */}
        <div style={{marginTop: 72, marginLeft: 340}}>
          {isUpdate ?
            <ButtonWrap onClick={async () => {
              await onsubmitFormUpdate()
            }}>
              <div style={{
                width: 360,
                height: 46,
                boxSizing: 'border-box',
                paddingTop: '9px'
              }}>
                <p style={{fontSize: 18}}></p>
              </div>
            </ButtonWrap>
            :
            <ButtonWrap onClick={async () => {
              await onsubmitForm()
            }}>
              <div style={{
                width: 360,
                height: 46,
                boxSizing: 'border-box',
                paddingTop: '9px'
              }}>
                <p style={{fontSize: 18}}></p>
              </div>
            </ButtonWrap>
          }
        </div>
      </WhiteBoxContainer>
    </div>
  )
}


const ButtonWrap = Styled.button`
    padding: 4px 12px 4px 12px;
    border-radius: 5px;
    color: black;
    background-color: ${POINT_COLOR};
    border: none;
    font-weight: bold;
    font-size: 13px;s
`

export default CustomerRegister
