import client from '../configs/basic';

/**
 * getMoldData()
 *    
 * @param {string} url  
 * @returns {object} data object
 * @author 
 * @version 0.1
 */

export const getMoldData = async (url: string) => {
    const temp: IServerData = await client.get(url);
    if (temp.status === 400) {
        alert(' .')
        return
    }
    return temp.results!;
}

