/**
 * getClutchData()
 *    
 * @param {string} url  
 * @returns {object} data object
 * @author 
 */
export const getCluchData = async (url: string, bodyData: object) => {
    const temp: IServerData = await client.post(url, bodyData);
    if (temp.status === 400) {
        alert(' .')
        return
    }
    return temp.results!;
}

